/**
 *   Name: Bizagi Smartphone Render Yes-No Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the Yes-No render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.yesno", bizagi.rendering.yesno.prototype);
// Extends itself
bizagi.rendering.smartphone.yesno.extend("bizagi.rendering.smartphone.yesno", {}, {

    /**
     * Method to customize each render after processed
     */
    postRender: function () {
        var $control = this.getControl();
        this.radio = $control.find("input[type=radio]");

        if (this.properties.editable && (this.value == null || this.value === undefined) && this.properties.value != null) {
            this.value = this.properties.value;
        }
    },

    /**
     * Method to render non editable values
     * @returns {*|jQuery}
     */
    renderReadOnly: function () {
        var template = this.renderFactory.getTemplate("readonly");

        return $.fasttmpl(template, {value: this.getDisplayValue()});
    },

    /**
     * Method to customize each render after processed in read-only mode
     */
    postRenderReadOnly: function () {
        var $control = this.getControl();
        this.radio = $control.find("input");

        // Call base
        this._super();
    },

    /**
     * Sets the value in the rendered control
     * @param {} value
     */
    setDisplayValue: function (value) {
        var $control = this.getControl();
        var parsedValue = bizagi.util.parseBoolean(value);

        this.setValue(parsedValue, false);

        if (this.properties.editable) {
            if (parsedValue) {
                $control.find("input[value=true]").prop('checked', true);
                $control.find("input[value=false]").prop('checked', false);
            } else {
                $control.find("input[value=false]").prop('checked', true);
                $control.find("input[value=true]").prop('checked', false);
            }
        } else {
            this.radio.val((this.getDisplayValue()));
        }
    },

    /**
     * Method to customize the render's behaviour to add handlers
     */
    configureHandlers: function () {
        var self = this;

        self.radio.change(function () {
            var $selected = $(this);
            var value = $selected.prop('value');

            if (self.value !== value) {
                self.setValue(value);
            }
        });
    },

    /**
     * Context to apply styles
     * @param context
     * @returns {jQuery|HTMLElement}
     */
    getElementsToStylize: function (context) {
        return $("label.bz-radio__label", context);
    },

    /**
     * Cleans current data
     */
    cleanData: function () {
        var value = {id: "", label: ""};
        this.setValue(value, false);

        this.clearDisplayValue();
    },

    /**
     * Sets the value in the rendered control
     */
    clearDisplayValue: function () {
        if (this.properties.editable) {
            this.radio.prop('checked', false);
        } else {
            this.radio.val(this.getDisplayValue());
        }
    }

});
