/**
 *   Name: Bizagi Smartphone Render Grid Offline Extension
 *   Author: Daniel Ordonez
 *   Comments:
 *   -   This script will redefine the offline grid render class to adjust to smartphone devices
 */

// Extends itself
bizagi.rendering.smartphone.grid.extend("bizagi.rendering.smartphone.grid.offline", {}, {
    /*
    *   Constructor
    */
    init: function (params) {
        var self = this;
        var originalRows = bizagi.clone(params.data.properties.data.rows);     
        var completeRows = bizagi.clone(params.data.properties.data.rows);     
        // Call base
        this._super(params);
        var properties = this.properties;
        properties.data.originalRows = originalRows;
        properties.data.completeRows = completeRows;
        // Check if offline forms is supported
        this.isOfflineForm = bizagi.util.isOfflineForm({context: self});

        // Fill default properties
        var form = self.getFormContainer();        
    
        properties.isTaskForm = form.properties.isTaskForm
        self.newRecords = {};
        //Preload new lines previously added offline
        if(properties["new-records"]) {            
            var newRowKeys = Object.keys(properties["new-records"]);
            for(var k = 0; k< newRowKeys.length;k++) {
                self.newRecords[newRowKeys[k]] = { createdOffline: true };
                self.changes[newRowKeys[k]] = properties["new-records"][newRowKeys[k]];                
            }
        }
        self["deleted-records"] = properties["deleted-records"] ? properties["deleted-records"] : {};

        if (properties.groupBy || properties.groupSummary) {
            // Add warning
            form.addWarning(self.getResource("render-tablet-warning-grid"));
        }
    },

    /*
    *   Template method to implement in each device to customize each render after processed
    */
    postRender: function () {
        var self = this;
        var properties = self.properties;
        var control = self.getControl();

        self.initialLoadDone = false;

        // Find component holder
        self.grid = $(">div", control);

        // Hide common label
        self.getLabel().hide();

        // Grid plugin
        self.applyGridPlugin();

        // Just for smartphones we need to take only one row if there is data in the json, because we use just one row per page
        if (properties.data && properties.data.completeRows && properties.data.completeRows.length > 0) {
            var newData = [];
            newData.push(properties.data.completeRows[0]);
            properties.data.rows = newData;
        }

        // Set initial data
        if (properties.data) {

            self.updateData(properties.data);

            // Trigger change in order to start up the actions when the controls is ready
            $.when(self.ready()).done(function () {
                    self.triggerRenderChange({changed: false});
                });
        }

        // Execute pending actions
        self.initialLoadDone = true;

        for (var i in self.pendingActions) {
            self.pendingActions[i].method.apply(self, self.pendingActions[i].arguments);
        }
    },

    /*
    *   Method to render non editable values
    */
    postRenderReadOnly: function () {
        var self = this;

        // Do the same as the post-render            
        self.postRender();
    },

    /*
    *   Method to render non editable values
    */
    applyGridPlugin: function () {
        var self = this;
        var properties = self.properties;
        var grid = self.grid;
        var mode = self.getMode();

        // Build column metadata
        var columns = self.buildColumnModel();

        // Apply grid plugin
        grid.bizagi_grid_smartphone({
            columns: columns,
            title: properties.displayName,
            isOfflineForm: this.isOfflineForm || false,
            mode: self.getMode(),
            template: {
                grid: self.renderFactory.getTemplate("bizagi.grid.grid"),
                waiting: self.renderFactory.getTemplate("bizagi.grid.waiting"),
                table: self.renderFactory.getTemplate("bizagi.grid.table"),
                emptyTable: self.renderFactory.getTemplate("bizagi.grid.table.empty"),
                column: self.renderFactory.getTemplate("bizagi.grid.column"),
                specialColumn: self.renderFactory.getTemplate("bizagi.grid.column.special"),
                row: self.renderFactory.getTemplate("bizagi.grid.row"),
                rowButtons: self.renderFactory.getTemplate("bizagi.grid.row.buttons"),
                cell: self.renderFactory.getTemplate("bizagi.grid.cell"),            
                pager: self.renderFactory.getTemplate("bizagi.grid.pager"),                
                specialCell: self.renderFactory.getTemplate("bizagi.grid.offline.cell.special"),
                buttons: self.renderFactory.getTemplate("bizagi.grid.buttons")
            },
            actions: {
                add: properties.allowAdd,
                edit: (properties.allowEdit && properties.withEditForm && properties.data != null),
                remove: properties.allowDelete,               
                inlineAdd: properties.inlineAdd,
                details: false,
                allowMore: false,
                enableXlsExport: false,
                enablePdfExport: false,
                hasTotalizer: false
            },
            isValid: function () {
                return self.isValidGrid();
            },
            pageRequested: function (ui) {
                if (mode !== "execution") return;
                self.onPageRequested(ui)
            },
            totalData: function(ui) {
                if (mode != "execution") return;
                //self.onTotalData();
            },
            drawCell: function (ui) {
                return self.drawCell(ui.column, ui.key, ui.value, ui.isNewRow);
            },
            cellReady: function (ui) {
                return self.onCellReady(ui.column, ui.key, ui.cell, ui.isNewRow);
            },
            rowSelected: function (ui) {                
                //self.triggerHandler("rowSelected", {id: ui.key, data: ui});
            },
            addRow: function (ui) {
                if (mode !== "execution") return;
                self.addRow();
            },
            editRow: function (ui) {
                if (mode !== "execution") return;
                self.editRow(ui.key);
            },
            deleteRow: function (ui) {
                if (mode !== "execution") return;
                self.deleteRow(ui.key);
            },
            showMore: function (ui) {
                self.showMore(ui.key);
            }
        });
    },

    /*
    *   Method to fetch data from the server and then update the data
    */
    fetchData: function (page, sortBy, sortType) {
        var self = this;
        var properties = self.properties;

        // Define defaults
        page = page || 1;
        sortBy = sortBy || "";
        sortType = sortType || "asc";

        // Update control properties
        properties.page = page;
        properties.sort = sortBy + " " + sortType;

        self.dataReadyDeferred = new $.Deferred();

        // Start loading to avoid multiple calls to the server
        self.startLoading();

        $.when(self.getRemoteData()).then(function (data) {
            self.endLoading();
            if (data) {
                self.updateData(data);
                self.triggerRenderChange();
                self.dataReadyDeferred.resolve();
            }
        });
    },

    /*
    * Method not required in smartphone implementation
    */
    processChanges: function () {
        var self = this;
        var rowsList = [];
        return rowsList;
    },

    /*
    *   Add the render data to the given collection in order to send data to the server
    */
    collectData: function (renderValues) {
        var self = this;
        var errorsMessage = [];
        var internalRedersValues = [];
        var properties = self.properties;
        var data = self.properties.data;
        var temporalNew = {};
        // Save pending inline add rows
        if (self.isValid(errorsMessage)) {
            for (var id in self.changes) {
                var sortArray = [];
                var currentChanges = {};                
                for (var columnXpath in self.changes[id]) {

                    // Check if the render can submit data
                    if (self.canColumnBeSent(id, columnXpath)) {
                        var columnNumber = self.getColumn(columnXpath).columnIndex - 1;
                        var value = self.changes[id][columnXpath];

                        if (typeof value === "string") {
                            if (value.match(/\n/gm) !== null) {
                                value = value.replaceAll("\n", "\\n");
                            }
                        }

                        sortArray[columnNumber] = value;                                  
                        var xpath = properties.xpath + self.buildCellXpathFilter(id) + "." + columnXpath;
                        var columnValue = self.changes[id][columnXpath];
                        var value = columnValue && columnValue.value ? columnValue.value : columnValue;                                
                        renderValues[xpath] = value;
                        //Changes are store to delete them if the row is new, the rest of changes can be saved by xpath
                        currentChanges[xpath] = value;
                    }
                }
                //If new record store action data
                if(self.newRecords[id]) {
                    //Use - to avoid issues with possible xpaths in renderValues used both in new and deleted records
                    var addRowData = {
                        url: properties.addUrl,
                        idRender: properties.id,
                        xpath: properties.xpath,
                        xpathContext: properties.xpathContext,
                        contexttype: properties.contexttype,
                        idPageCache: properties.idPageCache,
                        submitData: self.changes[id],
                        prepare: true,
                        idRow: parseInt(id+'')
                    }
                    temporalNew[id] = addRowData;
                    for(change in currentChanges) {
                        delete renderValues[change];
                    }
                    var details = {}; 
                    details.idRow = parseInt(id+'');     
                    details.isNew = true; 
                    details.changes = self.changes[id]; 
                    sortArray.push(details); 
                    if (!self.newRecords[id].createdOffline)                         
                        internalRedersValues[internalRedersValues.length] = sortArray;                                            
                } 
            }
        }

        // Get data
        if (JSON.stringify(data.completeRows) !== JSON.stringify(data.originalRows) || internalRedersValues.length > 0) {
            renderValues[properties.xpath] = renderValues[properties.xpath]? renderValues[properties.xpath]: {} 
            if (typeof (data.completeRows) !== "undefined" && data.completeRows.length > 0) {
                renderValues[properties.xpath].rows = self.processOldRows(data.completeRows);
            } else {
                renderValues[properties.xpath].rows = internalRedersValues;
            }
            if(self["deleted-records"] && Object.keys(self["deleted-records"]).length > 0 )
                renderValues[properties.xpath]["deleted-records"] = self["deleted-records"];
            if(temporalNew && Object.keys(temporalNew).length > 0)
                renderValues[properties.xpath]["new-records"] = temporalNew;
        }
        

    },

    /*
    *   Delete field id array objects
    */
    processOldRows: function (rows) {
        var newRows = [];
        var tempRows = rows;
        var self = this;

        for (var col = 0, leng = tempRows.length; col < leng; col++) {
            var temp = tempRows[col];
            newRows[col] = $.grep(temp, function (n, i) {
                return i > 0;
            });
            var details = {}; 
            details.idRow = parseInt(temp[0]+'');     
            details.isNew = self.newRecords[temp[0]]? true : false; 
            details.changes = self.changes[temp[0]] ? self.changes[temp[0]] : {}; 
            newRows[col].push(details); 
        }

        return newRows;
    },
    /*
    *   Delete field id array
    */
    processRow: function (row) {
        return $.grep(row, function (n, i) {
            return i > 0;
        });
    },

    onPageRequested: function (ui) {
        var self = this;
        self.fetchData(ui.page, ui.sortBy, ui.sortType);
    },

    /*
    *   Customizes the content drawing inside the cell
    */
    drawCell: function (column, key, value, isNewRow) {
        var self = this;

        if (column.key) return value;

        if (typeof (value) === "object" && value!==null && !isNewRow) {
            value = value.hasOwnProperty("value") ? value.value : value;
        }

        // Get render column                
        var columnIndex = column.index - 1;
        var renderColumn = self.columns[columnIndex];
        var cellOverride = self.getCellOverride(key, column.index - 1);

        self.newRecords = self.newRecords || {};

        // Get cell properties
        if (!self.cellMetadata[key]) self.cellMetadata[key] = {};
        if (!self.cellMetadata[key][columnIndex]) self.cellMetadata[key][columnIndex] = {};
        var properties = self.cellMetadata[key][columnIndex].properties || renderColumn.originalProperties;

        var visible = properties.visible !== undefined ? properties.visible : true;
        var editable = properties.editable !== undefined ? properties.editable : true;

        // Apply overrides from cellOverrides
        visible = cellOverride.visible;
        editable = properties.editable ? cellOverride.editable : false;

        // Show editable cell when we are performing inline add operation
        if (isNewRow) editable = true;

        // If the cell we are drawing is the summary cell, we need to make it readonly
        if (key === "summary") editable = false;

        renderColumn.properties.displayType = 'value';
        renderColumn.setValue(key, value);
        renderColumn.setSurrogateKey(key);
        renderColumn.properties.data = renderColumn.properties.data || [];

        // Column search
        if (renderColumn.properties.type === "columnSearch" && value != null) {
            for (var i = 0, len = renderColumn.properties.data.length; i < len; i++) {
                if (renderColumn.properties.data[i].id == value) {
                    value = {id: renderColumn.properties.data[i].id, label: renderColumn.properties.data[i].value};
                }
            }
        }

        if ((renderColumn.properties.type === "columnCombo" || renderColumn.properties.type === "columnList") && value != null) { //|| renderColumn.properties.type == "columnRadio"
            for (var i = 0, len = renderColumn.properties.data.length; i < len; i++) {
                if (renderColumn.properties.data[i].id == value) {
                    value = [[renderColumn.properties.data[i].id, renderColumn.properties.data[i].value]];
                }
            }
        }

        // Column text
        if (renderColumn.properties.type === "columnText" && bizagi.util.isEmpty(value) && value != null) {
            value = "";
        }

        // Do the render
        if (visible) {
            if (editable) {
                return self.drawEditableCell(renderColumn, properties, key, value, isNewRow);
            } else {
                return self.drawReadonlyCell(renderColumn, properties, key, value);
            }
        } else {
            return "";
        }
    },

    /*
    *   Draws the editable version for the cell
    */
    drawEditableCell: function (renderColumn, properties, key, value, isNewRow) {
        var self = this;
        var defer = new $.Deferred();

        // Set editable
        var originalEditable = renderColumn.properties.editable;
        renderColumn.properties.editable = true;

        // Fix problem when required property lose
        renderColumn.overrideProperties({required: properties.required});

        // Render cell
        $.when(renderColumn.render(key, value)).done(function (cell) {
            // Register array with new records, just when this is a new-record
            if (isNewRow && !self.newRecords[key]) {
                self.newRecords[key] = {};
            }

            // convert all row visible
            for (var j = 0; j < self.columns.length; j++) {
                self.cellOverrides[key] = self.cellOverrides[key] || {};
                self.getCellOverride(key, j).visible = (self.getCellOverride(key, j).visible != undefined) ? self.getCellOverride(key, j).visible : true;
            }

            // Add change handler  
            renderColumn.getDecorated(key).bind("renderchange", function (render) {
                self.onCellChange(renderColumn, cell, key);
            });

            // Resolve cell
            defer.resolve(cell);
        });

        // Restore editable
        renderColumn.properties.editable = originalEditable;

        // Return promise
        return defer.promise();
    },

    /*
    *   Draws the readonly version for the cell
    */
    drawReadonlyCell: function (renderColumn, properties, key, value) {
        var result = "";

        // Use totalizer format when printing summary row
        if (key === "summary") {
            var originalFormat = renderColumn.properties.textFormat;
            if (renderColumn.properties.totalize) {
                renderColumn.properties.textFormat = renderColumn.properties.totalize.format;
            }

            result = renderColumn.renderSummary(key, value);

            // Restore column format
            renderColumn.properties.textFormat = originalFormat;
        } else {
            result = renderColumn.renderReadOnly(key, value);
        }
        return result;
    },

    /*
    *   Defines the handler when a cell changes its value
    */
    onCellChange: function (renderColumn, cell, key) {

        var self = this;
        var _value = renderColumn.getValue(key);
        var compositeValue = renderColumn.getCompositeValue(key);

        if (renderColumn.properties.type === "columnDate" && renderColumn.data.properties.showTime == true) {

            if (!bizagi.util.isEmpty(_value)) {
                var date = bizagi.util.dateFormatter.getDateFromInvariant(_value, true);
                _value = bizagi.util.dateFormatter.formatInvariant(date, true);
            } else if (_value === "") {
                _value = "";
            }
        } else if (renderColumn.properties.type === "columnDate") {

            if (!bizagi.util.isEmpty(_value)) {
                var date = bizagi.util.dateFormatter.getDateFromInvariant(_value, false);
                date.setHours(0, 0, 0, 0);
                _value = bizagi.util.dateFormatter.formatInvariant(date, true);
            } else if (_value === "") {
                _value = "";
            }
        }

        if (self.properties.allowEdit || self.grid.bizagi_grid_smartphone("isNewRecord", key)) {
            var trigger = self.grid.bizagi_grid_smartphone("isNewRecord", key) ? false : true;
            self.collectGridChange({
                id: key,
                xpath: renderColumn.properties.xpath,
                value: _value,
                compositeValue: compositeValue,
                columnIndex: renderColumn.columnIndex,
                trigger: trigger
            });
        }

        // Update change in grid's component data
        self.grid.bizagi_grid_smartphone("changeCellValue", key, renderColumn.columnIndex, _value);

        // If the column has totalizers then we need to refresh the summary
        if (renderColumn.properties.totalize && renderColumn.properties.totalize.operation) {
            self.grid.bizagi_grid_smartphone("refreshSummary", renderColumn.columnIndex);
        }
    },
    /*
    *   Collect a single cell change made in the grid to send the data when the user saves the form
    */
    collectGridChange: function (params) {
        var self = this;
        params = params || {};
        var id = params.id;
        var xpath = params.xpath;
        var value = params.value;
        var trigger = typeof (params.trigger) !== "undefined" ? params.trigger : true;

        self.changes[id] = self.changes[id] || {};
        self.changes[id][xpath] = value;

        if (trigger)
            self.triggerRenderChange({key: id, column: xpath});
    },

    /**
     *  Adds a row to the grid
     */
    addRow: function () {

        var self = this;
        var properties = self.properties;
        var data = properties.data;
        // Handle information stored offline based on data        
        var newData = [];
        var newRow = self.createAddRowData();
        newData.push(newRow);

        //Support for deletion and new rows
        self.newRecords[newRow[0]] = { createdOffline: true };

        data.rows = newData;
        data.completeRows.push(newRow);
        data.records++;
        data.page = data.records;        
        
        self.updateData(data);
        self.triggerRenderChange();
    },

    /**
     * Creates a dummy data for the new row
     * @returns {}
     */
    createAddRowData: function () {
        var self = this;
        
        var row = [];
        var key = Math.ceil(Math.random() * 100000);
        row.push(key);
        $.each(self.columns, function (i) {
                row.push(null);            
        });

        return row;
    },

    /**
     *  Edits a row in the grid
     */
    editRow: function (id) {
    },

    /**
     *  Deletes a row to the grid
     */
    deleteRow: function (id) {
        var self = this;

        if (!id || id == 0) {
            bizagi.showMessageBox(bizagi.localization.getResource("render-grid-message-no-selected-row"), bizagi.localization.getResource("render-grid-header-no-selected-row"));
            return;
        }

        bizagi.showConfirmationBox(this.getResource("render-grid-delete-confirmation")).done(function () {
            // Do a grid record deletion
            $.when(self.submitDeleteRequest(id))
                .done(function () {
                    // Reload grid
                    self.fetchData();
                });
        });
    },

    /**
     *  Edits the entire row (inline) in another slide
     */
    editInline: function (id) {
    },
    /*
    *   Retrieve the data for the new rows and push them into the server
    */
    saveInlineAddedRows: function (keys, bRefresh) {
        var self = this;
        var rows = self.processChanges();

        if (!bizagi.util.isEmpty(rows)) {
            self.properties.data.rows = $.merge(self.properties.data.rows, rows);
        }
    },

    /*
    * Removes invalid records 
    */
    removeNewRecords: function (ui) {
        var self = this;

        if (ui && ui.keys) {
            $.each(ui.keys, function (_, key) {
                if (self.newRecords[key]) {
                    delete self.newRecords[key];
                }
            });
        }
    },

    /*
    *   Fetch the data into a deferred
    */
    getRemoteData: function (params) {
        var self = this;
        var properties = self.properties;

        // Set params
        params = params || {};

        // Check if a custom method has been given
        if (properties.overrideGetRemoteData) {
            var result = properties.overrideGetRemoteData(params);
            return result != null ? result.promise() : null;
        }

        // Default ajax call
        var defer = new $.Deferred();

        
        // Resolve with remote data
        var data = properties.data;
        // Handle information stored offline based on data
        if (properties.data && properties.data.completeRows && properties.data.completeRows.length > 0) {
            var newData = [];
            newData.push(properties.data.completeRows[(properties.page-1)]);
            properties.data.rows = newData;
            properties.data.page = properties.page;
        }
        else if (properties.data.completeRows && properties.data.completeRows.length == 0 ) {
            properties.data.rows = [];            
        }                

        // Check RTL
        if (self.isRTL()) {
            data.rows = self.changeOrderData(data.rows);
        }

        defer.resolve(data);

        return defer.promise();
    },
    /*
    *   Submits a grid delete record request for the given id 
    *   Returns a deferred
    */
    submitDeleteRequest: function (id) {
        var self = this;
        var properties = self.properties;
        var data = self.properties.data;
        for (var i = 0, length = data.completeRows.length; i < length; i++) {
            if (data.completeRows[i][0] == id) {

                data.completeRows.splice(i, 1);
                i--;

                data.records--;
                //Enters if it was created online
                if(!self.newRecords[id])
                {
                    var xpath = properties.xpath + self.buildCellXpathFilter(id);

                    var delInfo = {
                        url: properties.deleteUrl,
                        idRender: properties.id,
                        xpath: xpath,
                        xpathContext: properties.xpathContext,
                        idPageCache: properties.idPageCache,
                        contexttype: properties.contextType,
                        prepare: true
                    }
                    self["deleted-records"][id] = delInfo;
                }else{
                    delete self.newRecords[id];
                    delete self.changes[id];
                }
                break;
            }
        }

        return (data.completeRows);
    },

    isValidGrid: function () {
        var self = this;
        var errorsMessage = [];
        var form = self.getFormContainer();

        if (typeof (form.validationController) !== "undefined")
            form.validationController.clearValidationMessages();

        //self.isValid(errorsMessage);

        if (errorsMessage.length > 0) {
            if (typeof (form.validationController) !== "undefined") {
                for (var index = 0; index < errorsMessage.length; index++) {
                    form.validationController.showValidationMessage(errorsMessage[index].message, errorsMessage[index].xpath);
                }
            }

            return false;
        } else {
            return true;
        }
    }
});