/**
 *   Name: Bizagi Smartphone Render Grouped Grid Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the Grouped Grid render class to adjust to smartphone devices
 */

// Extends from Grid Smartphone base
bizagi.rendering.smartphone.grid.extend("bizagi.rendering.smartphone.grid.grouped", {}, {

    /**
     * Method to customize each render after processed
     */
    postRender: function () {
        this.getControlFiller().addClass("bz-form-item__control--hidden");
    },

    /**
     * Method to customize each render after processed in read-only mode
     */
    postRenderReadOnly: function () {
        this.getControlFiller().addClass("bz-form-item__control--hidden");
    },

    isValidGrid: function (invalidElements) {
        return this._super(invalidElements);
    }

});