/**
 *   Name: Bizagi Smartphone Collection Navigator Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the collection Navigator class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.collectionnavigator", bizagi.rendering.collectionnavigator.prototype);
// Extends itself
bizagi.rendering.smartphone.collectionnavigator.extend("bizagi.rendering.smartphone.collectionnavigator", {}, {

    /**
     * Method to customize each render after processed
     */
    postRender: function () {
        this.getControlFiller().addClass("bz-form-item__control--hidden");
    },

    /**
     * Method to customize each render after processed in read-only mode
     */
    postRenderReadOnly: function () {
        this.getControlFiller().addClass("bz-form-item__control--hidden");
    }


});