/**
 *   Name: Bizagi Smartphone Render Geolocation Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the geolocation render class to adjust to Smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.geolocation", bizagi.rendering.geolocation.prototype);
// Extends itself
bizagi.rendering.smartphone.geolocation.extend("bizagi.rendering.smartphone.geolocation", {
    ALLOW_UPDATE: "update"
}, {

    /**
     * Template method to implement in each device to customize each render after processed
     */
    postRender: function () {
        var $control = this.getControl();
        this.coordinate = $control.find("input");

        // Call base
        this._super();
    },

    /**
     * Method to render non editable values
     * @returns {*|jQuery}
     */
    renderReadOnly: function () {
        var template = this.renderFactory.getTemplate("readonly");

        return $.fasttmpl(template, {value: this.getDisplayValue()});
    },

    /**
     * Method to customize each render after processed in read-only mode
     */
    postRenderReadOnly: function () {
        var $control = this.getControl();
        this.coordinate = $control.find("input");

        // Call base
        this._super();
    },

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers
     * @returns {}
     */
    configureHandlers: function () {
        var self = this;
        var $control = self.getControl();

        // If the control doesn't have a value set the position
        if (self.value == null) {
            self.updateDisplayValue();
        }

        $control.on("click", "[data-action=" + self.Class.ALLOW_UPDATE + "]", self.processAction.bind(self));

    },

    /**
     * Sets the value in the rendered control
     * @param {} value
     * @returns {}
     */
    setDisplayValue: function (value) {
        var self = this;
        var def = new $.Deferred();

        if (value != null && value !== "") {
            if (self.coordinate)
                self.coordinate.val(self.performPrecision(value));

            // Set internal value 
            self.setValue(value, false);
            def.resolve();
        } else {
            var onSuccess = function (position) {
                var coordinates = position.coords.latitude + " " + position.coords.longitude;

                self.coordinate.val(self.performPrecision(coordinates));

                // Set internal value 
                self.setValue(coordinates, false);
                def.resolve();
            };

            var onFail = function (message) {
                def.reject(message);
            };

            if (bizagi.util.isCordovaSupported() && bizagi.util.isiOSDevice()) {
                navigator.geolocation.getCurrentPosition(onSuccess, onFail);
            } else {
                self.getCurrentPosition(onSuccess, onFail);
            }
        }

        return def.promise();
    },

    updateDisplayValue: function () {
        var self = this;

        self.setDisplayValue().then(function () {
            self.setValidationMessage("");
        }).fail(function (message) {
            self.coordinate.val("");
            self.setValidationMessage(message);
        });
    },

    /**
     * Process an action in order to do something with the geolocation
     */
    processAction: function () {
        this.coordinate.empty();
        this.updateDisplayValue();
    },

    
    getElementsToStylize: function($context){
        return this.properties.editable ? $("input", $context) : $(".readonly-control", $context); 
    }
});
