/**
 *   Name: Bizagi smartphone Render list Extension
 *   Author: Bizagi UI Forms
 *   Comments:
 *   -   This script will redefine the list render class to adjust to smartphone devices
 */
// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.list", bizagi.rendering.combo.prototype);
// Extends itself
bizagi.rendering.smartphone.list.extend("bizagi.rendering.smartphone.list", bizagi.rendering.list.prototype);
// Extends itself
bizagi.rendering.smartphone.list.extend("bizagi.rendering.smartphone.list", {}, {
    
    /*
    *   Template method to implement in each children to customize each control
    */
    renderControl: function () {
        var template = this.renderFactory.getTemplate("list");

        return $.fasttmpl(template, {
            editable: this.properties.editable,
            value: this.getDisplayValue()
        });

    },

    /**
     * Add event handlers to the combo control
     */
    configureHandlers: function () {
        var self = this;
        var control = self.getControl();

        if (self.getMode() !== "execution") {
            return;
        }

        $(".bz-form-list", control).bind("click", function () {

            $(this).mobileView({
                customHeader: $.fasttmpl(self.renderFactory.getTemplate("modal-header-search"), {}),
                displayName: self.properties.displayName || "",
                orientation: self.properties.orientation || "",
                contentTemplate: "",

                close: function () {
                    self.triggerGlobalHandler("modalViewDidDisappear", {});
                },

                open: function () {
                    self.triggerGlobalHandler("closeModalValidationMessage", {});
                    self.triggerGlobalHandler("modalViewDidAppear", {});
                },

                configureActions: function (context) {
                    var params = {};
                    if (self.isDependant && self.properties && /^cascadingcombo$/i.test(self.properties.type) && self.getParentCombo) {
                        var parentValue = self.getParentCombo().getValue();
                        if (parentValue && parentValue.id) {
                            params[self.Class.BA_ACTION_PARAMETER_PREFIX + "parent"] = parentValue.id;
                        }
                    }

                    $.when(self.renderCombo(params)).done(function (list) {
                        $(".bz-modal-view__content", context.content).html(list);
                        self.configureModalViewHandlers(context);
                    })
                }
            });
        });

    },

    processComboData: function (data) {
        var self = this;
        data.forEach(function (value) {
            self.validateValue(value);
        });
    },

    /**
     * Setting the display value, always you call a setDisplayValue yor are performinng a setValue under the hood.
     * */
    setDisplayValue: function (value) {
        var comboValue;
        value = this.mapValue(value);

        if (value && (typeof value === "object" && typeof value.label !== "undefined")) {

            if (value.label !== "" && !bizagi.util.isNumeric(value.label)) {
                comboValue = value.label;
            } else {
                var selected = this.findDataValueById(value.value);
                comboValue = selected ? selected.value : "";
            }

            if ($.isArray(comboValue)) {
                comboValue = comboValue.join(" - ");
            }
            value = ({ "id": value.id, "value": comboValue });
        }

        var control = this.getControl();
        $("input", control).val(this.formatItem(value.value));

        //Set Value
        this.selectedValue = value;
        this.setValue(value);
    },

    /**
     * Set the handlers for the modal elements
     * @param {kendoModalObject} context full modal object
     */
    configureModalViewHandlers: function (context) {
        var self = this;

        $(".bz-list__item", context.content).on("click", function () {
            var valueObjet = {
                "id": $(this).data("value"),
                "value": $(this).text()
            };

            if (valueObjet.id == "") {
                valueObjet.value = "";
            }

            self.setDisplayValue(valueObjet);
            self.trigger("select", [valueObjet, false]);
            context.close();
        });

        var $elementsList = $(".bz-list li", context.content);
        var $clearButton = $(".bz-modal-view__clear", context.header);
        var $searchInput = $(".bz-modal-view__search", context.header);

        $("[data-action=close]", context.header).bind("click", function () {
            context.close();
        });

        $clearButton.bind("click", function () {
            $searchInput.val("").trigger("change");
        });
        // Filtering list
        $searchInput.bind("change keyup", function () {
            if (this.value == "") {
                $clearButton.removeClass("bz-modal-view__clear--visible");
            } else {
                $clearButton.addClass("bz-modal-view__clear--visible");
            }
            self.filterList(this.value, $elementsList);
        }).focus();
    },

    /**
     * Filtering list of elements
     * @returns {}
     */
    filterList: function (search, $li) {

        $li.filter(function () {
            var stringContainer = $(this).text().toUpperCase();
            var stringToSearch = search.toUpperCase();

            if (stringContainer.indexOf(stringToSearch) >= 0) {
                return $(this);
            } else {
                $(this).hide();
            }
        }).show();
    },

    /**
     * Print the list of elements to show in the modal
     * @param {object} data list of elements to print
     */
    renderComboTemplate: function (data) {
        var template = this.renderFactory.getTemplate("list-items");
        var value = this.getValue();
        value = (value !== null && typeof value !== "undefined") ? value : {"id": "", "value": ""};

        return $.fasttmpl(template, {
            "items": data,
            "selectedId": value.id,
            "hasItems": data.length > 0
        }, {
            "validateItem": this.validateValue
        });
    },


    getElementsToStylize: function ($context) {
        return this.properties.editable ? $("input", $context) : $(".readonly-control", $context);
    }
});
