/**
 *   Name: Bizagi Smartphone Render Upload Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the upload render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.upload", {
    // Statics
    BA_ACTION_PARAMETER_PREFIX: bizagi.render.services.service.BA_ACTION_PARAMETER_PREFIX,
    BA_CONTEXT_PARAMETER_PREFIX: bizagi.render.services.service.BA_CONTEXT_PARAMETER_PREFIX,
    BA_PAGE_CACHE: bizagi.render.services.service.BA_PAGE_CACHE
}, bizagi.rendering.upload.prototype);

// Extends itself
bizagi.rendering.smartphone.upload.extend("bizagi.rendering.smartphone.upload", {
    QUALITY_PICTURE: 50,
    LIMIT: 1, // The maximum number of media in the device user can record in a single capture operation.
    EXTENSIONSIMG: ["image/jpeg", "jpeg", "image", "png", "jpg", "bmp"],
    EXTENSIONSFILE: ["doc", "docx", "xls", "xlsx", "ppt", "pptx", "pdf", "zip", "rar", "txt"],
    EXTENSIONSVIDEO: ["video/quicktime", "quicktime", "qt", "mov"],
    EXTENSIONSAUDIO: ["audio/wav", "audio", "wav", "mp3"]
}, {
    /**
     * Whether or not is a native device
     * @var isNative boolean
     */
    isNative: false,

    /**
     * Whether or not we are offline
     * @var isOfflineForm boolean
     */
    isOfflineForm: false,

    /**
     * Update or init the element data
     * @param {} data
     */
    initializeData: function (data) {
        // Call base
        this._super(data);

        // Check if offline forms is supported
        this.isOfflineForm = bizagi.util.isOfflineForm({context: this});
        this.isNative = bizagi.util.isCordovaSupported();
    },

    /**
     * Template method to implement in each children to customize each control
     * @returns {}
     */
    renderControl: function () {
        var template = this.renderFactory.getTemplate("upload");

        return this.renderItem(template, {});
    },

    /**
     * Method to render non editable values
     * @returns {*|jQuery}
     */
    renderReadOnly: function () {
        var template = this.renderFactory.getTemplate("upload-readonly");
        var data = {
            noFiles: this.filesCount === 0
        };

        return this.renderItem(template, data);
    },

    /**
     * Method to render items
     * @returns {*|jQuery}
     */
    renderItem: function (template, data) {
        var self = this;
        var items = "";

        // Render template
        var html = $.fasttmpl(template, data);

        // Render current children
        for (var i = 0; i < self.filesCount; i++) {
            var file = {id: self.files[i][0], fileName: self.files[i][1]};
            var item = self.renderUploadItem(file);
            items += item;
        }

        html = self.replaceFilesHtml(html, items);

        return html;
    },

    /**
     * Template method to implement in each device to customize each render after processed
     */
    postRender: function () {
        var $control = this.getControl();

        this.$contentFile = $(".bz-file-upload__container", $control);
        this.$inputFile = $(".bz-file-upload__input", $control);
        this.$nativeFile = $(".bz-file-upload__native", $control);        
    },

    /**
     * Add action handlers
     */
    configureHandlers: function () {
        this.checkFileExtensions();
        this.attachEvents();
    },

    /**
     * Method to customize the render's behaviour to add handlers when the control is readonly
     */
    configureHandlersReadOnly: function () {
        var self = this;
        var $control = self.getControl();

        // Handler to download a file
        $("ul.bz-readonly-lists", $control).on("click", ".bz-file .bz-file-name", function () {
            var item = $(this).parent(".bz-file");
            var file = item.data();

            bizagi.loading.start();

            $.when(bizagi.util.media.downloadFile(file.url, file.title)).fail(function (error) {
                self.executeFormFailHandler(error);
            }).always(function () {
                bizagi.loading.stop();
            });
        });
    },

    /**
     * Method to configure the actions with control
     * @return {}
     */
    attachEvents: function () {
        var self = this;
        var $control = self.getControl();
        var counterChoseFiles = 0;
        // Attach all events
        this.$contentFile.off("click").on("click", function (evt) {
            if(!self.isNative || counterChoseFiles > 0){                
                self.chooseFile();             
            }else{
                bizagi.showMessageBox(bizagi.localization.getResource("render-upload-error-files-extension-supported-app", "You can only add images or videos."), 'Userfield error');
            }            
            
        });

        // Handler to download the file
        $("ul.bz-files", $control).on("click", ".bz-file .bz-file-name", function () {
            var item = $(this).parent(".bz-file");
            var file = item.data();

            bizagi.loading.start();

            $.when(bizagi.util.media.downloadFile(file.url, file.title)).fail(function (error) {
                self.executeFormFailHandler(error);
            }).always(function () {
                bizagi.loading.stop();
            });
        });

        // Attach delete option & Removes a file from the upload control
        $("ul.bz-files", $control).on("click", ".bz-file .bz-icn", function () {
            var item = $(this).parent(".bz-file");
            var file = item.data();

            self.removeFile(item, file.id);
        });

        if (self.isNative) {
            // Upload files
            var actions = self.getUploadChoices();
            counterChoseFiles = actions.length;
            this.$nativeFile.off("click").actionSheet({                
                actions: actions,
                actionClicked: function (action) {
                    self.handlerUploadFile(action.guid);
                },
                appear: function (action) {
                    self.triggerGlobalHandler("closeModalValidationMessage", {});
                    self.triggerGlobalHandler("modalViewDidAppear", {});
                },
                cancelClicked: function (action) {
                    self.triggerGlobalHandler("modalViewDidDisappear", {});
                }
            });

            // Resolution upload
            $(".bz-file-upload__resolution", $control).off("click").actionSheet({
                actions: bizagi.util.media.getImageResolutionSizes(),
                withoutCancel: true,
                actionClicked: function (action) {
                    bizagi.loading.start();

                    $.when(self.handlerResolutionUpload(action.guid)).always(function () {
                        bizagi.loading.stop();
                    });
                },
                appear: function (action) {
                    self.triggerGlobalHandler("closeModalValidationMessage", {});
                    self.triggerGlobalHandler("modalViewDidAppear", {});
                },
                cancelClicked: function (action) {
                    self.triggerGlobalHandler("modalViewDidDisappear", {});
                }
            });
        } else {

            // Attach event to web upload-control
            this.$inputFile.off("change").on("change", function () {
                self.addFile();
                this.value = "";
            });

            // Just in case
            this.$inputFile.off("click").on("click", function (evt) {
                evt.stopPropagation();
            });
        }
    },

    /**
     * Handler of upload files
     * @param {} ordinal
     * @returns {}
     */
    handlerUploadFile: function (ordinal) {
        var self = this;
        var actionSelected = ordinal || 0;

        switch (actionSelected) {
            case 2:

                navigator.camera.getPicture(function (dataImage) {
                    self.dataImage = dataImage;
                    var extensions = self.Class.EXTENSIONSIMG.concat(self.Class.EXTENSIONSVIDEO);

                    bizagi.loading.start();

                    $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties))
                        .then(function () {
                            //Gets the real properties for the file
                            return $.when(bizagi.util.media.getFileDataInfo(self.dataImage))
                                .then(function (fileData) {
                                    self.fileProperties = fileData;

                                    if (bizagi.util.media.checkFileTypes(fileData, self.properties, extensions)) {
                                        // "new Blob" constructor is not supported in android version 4.2.2
                                        var version = bizagi.util.getAndroidVersion();

                                        if ((version && version <= 4.2) || self.isOfflineForm) {
                                            return self.saveImage(self.dataImage);
                                        } else {

                                            if (!bizagi.util.isAndroid()) {
                                                return $.when(bizagi.util.media.modalResolutionUpload()).then(function (buttonIndex) {
                                                    if (buttonIndex <= bizagi.util.media.getImageResolutionItems().length) {
                                                        return self.handlerResolutionUpload(buttonIndex);
                                                    }
                                                });

                                            } else {
                                                // Force click to show actionSheet menu
                                                $(".bz-file-upload__resolution", self.getControl()).click();
                                            }
                                        }
                                    }
                                });
                        }).always(function () {
                        bizagi.loading.stop();
                    });

                }, self.onFail.bind(self), {
                    quality: self.Class.QUALITY_PICTURE,
                    correctOrientation: true,
                    sourceType: Camera.PictureSourceType.PHOTOLIBRARY
                });
                break;
            case 1:
                navigator.camera.getPicture(function (dataImage) {
                        bizagi.loading.start();

                        $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties)).then(function () {
                                return $.when(bizagi.util.media.getFileDataInfo(dataImage)).then(function (fileData) {
                                    self.fileProperties = fileData;
                                    return self.saveImage(dataImage);
                                });
                            }).always(function () {
                            bizagi.loading.stop();
                        });
                    },
                    self.onFail.bind(self), {
                        quality: self.Class.QUALITY_PICTURE,
                        sourceType: navigator.camera.PictureSourceType.CAMERA,
                        mediaType: navigator.camera.MediaType.PICTURE,
                        destinationType: Camera.DestinationType.FILE_URI,
                        correctOrientation: true,
                        targetWidth: 1280,
                        targetHeight: 960
                    });
                break;
            case 3:
                navigator.device.capture.captureVideo(function (dataVideo) {
                    bizagi.loading.start();

                    $.when(bizagi.util.media.checkMaxSizeVideo(dataVideo, self.properties))
                        .then(function () {
                            return self.saveVideo(dataVideo);
                        }).always(function () {
                        bizagi.loading.stop();
                    });

                }, self.onFail.bind(self), {limit: self.Class.LIMIT});
                break;
        }
    },

    /**
     * Handler of resolution upload
     * @param {} ordinal
     * @returns {}
     */
    handlerResolutionUpload: function (ordinal) {
        var self = this;
        var actionSelected = ordinal || 0;

        // If original, save image normally, if not, reduce quality and size
        if (actionSelected === bizagi.util.media.ResolutionType.ORIGINAL) {
            return self.saveImage(self.dataImage);
        } else {
            var resolution = bizagi.util.media.getImageResolution(actionSelected);

            return $.when(bizagi.util.media.transformImageSize(self.dataImage, resolution)).then(function (result) {
                self.editedImageURL = result.localUrl;
                self.properties.url = result.base64Image;

                return self.saveImage(self.dataImage);
            });
        }
    },

    /**
     * Send image using HTTP
     * @param {} dataImage
     */
    saveImage: function (dataImage) {
        var self = this;
        var dataItem = "";

        if (self.editedImageURL) {
            dataItem = self.editedImageURL;
        } else if (self.fileProperties) {
            dataItem = self.fileProperties.url;
        }

        // Fix android 4.4 getting images from recent folder
        var dataFile = bizagi.util.media.getImagePath(dataItem);
        var data = {
            dataFile: dataFile,
            fileName: self.fileProperties.name,
            mimeType: "image/jpeg"
        };

        // Cleans variables related previous uploads
        self.editedImageURL = null;
        self.fileProperties = null;

        return this.uploadFile(data);
    },

    /**
     * Method to save a video file
     * @param {} dataVideo
     * @returns {}
     */
    saveVideo: function (dataVideo) {
        var data = {
            dataFile: dataVideo[0].fullPath,
            fileName: dataVideo[0].name,
            mimeType: "video/quicktime"
        };

        return this.uploadFile(data);
    },

    /**
     * Method to save a file
     * @param data
     * @returns {*|void}
     */
    uploadFile: function (data) {
        var self = this;
        var params = {};
        var addParams = self.buildAddParams();
        addParams.queueID = "q_" + bizagi.util.encodeXpath(self.getUploadXpath());

        params.data = addParams;
        params.options = new FileUploadOptions();
        params.options.fileKey = "file";
        params.options.fileName = data.fileName;
        params.options.uploadKey = {key: addParams["h_xpath"]};
        params.options.addUrl = self.properties.addUrl;
        params.dataFile = data.dataFile;

        if (data.mimeType)
            params.options.mimeType = data.mimeType;

        return $.when(bizagi.util.media.uploadFile(params)).then(function (result) {
            var response = JSON.parse(decodeURIComponent(result.response));

            if (response.type === "error") {
                bizagi.showMessageBox(response.message, response.errorType);
            } else {
                self.onUploadFileCompleted(response);
            }
        }).fail(function (error) {
            self.executeFormFailHandler(error);
        });
    },

    /**
     * onFail
     * @param {} error
     */
    onFail: function (error) {
        var messageError = bizagi.util.processFailMessage(error);

        bizagi.loading.stop();

        if (messageError && typeof (messageError) === "string" && messageError.indexOf("No Activity found to handle Intent") !== -1) {
            var hasResource = bizagi.util.isValidResource("render-upload-error-media-app");
            var message = hasResource ? bizagi.localization.getResource("render-upload-error-media-app") : "Your device does not have an app to execute this action.";

            bizagi.showMessageBox(message);
        }
    },

    /**
     * onUploadFileCompleted
     * @param {} result
     */
    onUploadFileCompleted: function (result) {
        var self = this;
        var $wrapper = $(".bz-files", self.getControl());

        if (result.id && result.fileName) {
            // Locate it before the upload wrapper
            var newItem = self.renderUploadItem(result);
            $wrapper.append(newItem);

            // Increment counter
            self.filesCount = self.filesCount + 1;

            // Copy the files value but not its reference
            var files = self.files.slice();

            //Add the new item to this temporal array
            files.push([result.id, result.fileName]);

            // Update value
            self.setValue(files, true);

            // Check maxFiles
            self.checkMaxFiles();

            self.editedImageURL = null;
        } else {
            bizagi.showMessageBox(result.message, "Error");
        }
    },

    /**
     * Check maximum number of files
     */
    checkMaxFiles: function () {
        if (this.isMaxFilesReached()) {
            $(this.$contentFile).addClass("bz-file-upload__container--hidden");
        } else {
            $(this.$contentFile).removeClass("bz-file-upload__container--hidden");
        }
    },

    /**
     * Check if the maximum number of files than can be uploaded has been reached
     * @returns {}
     */
    isMaxFilesReached: function () {
        var maxFiles = this.properties.maxfiles;
        var actualFiles = this.properties.value ? this.properties.value.length : 0;

        return maxFiles !== 0 && (this.filesCount >= maxFiles && actualFiles >= maxFiles);
    },

    /**
     * Check allow extensions and define possible choices
     * @returns {}
     */
    checkFileExtensions: function () {
        var self = this;
        var validExtensions = self.properties.validExtensions || "";

        self.validOptions = {video: false, image: false, file: false};

        // Preprocess the extensions string
        validExtensions = validExtensions.replace(/\s+/g, "").toLowerCase();

        if (typeof (validExtensions) === "undefined" || validExtensions === "") {
            self.validOptions = {video: true, image: true, file: true};
        } else {
            validExtensions = validExtensions.split(";");

            if (validExtensions.length === 1 && validExtensions[0].indexOf("*.*") !== -1) {
                self.validOptions.video = true;
                self.validOptions.image = true;
            } else {
                for (var i = 0, length = validExtensions.length; i < length; i++) {
                    validExtensions[i] = validExtensions[i].replace("*.", "");

                    if (self.Class.EXTENSIONSFILE.toString().indexOf(validExtensions[i]) >= 0) {
                        self.validOptions.file = true;
                    } else if (self.Class.EXTENSIONSIMG.toString().indexOf(validExtensions[i]) >= 0) {
                        self.validOptions.image = true;
                    } else if (self.Class.EXTENSIONSVIDEO.toString().indexOf(validExtensions[i]) >= 0) {
                        self.validOptions.video = true;
                    }
                }
            }
        }

        // Media is not supported then upload control is disabled
        if (!self.validOptions.file && !self.validOptions.image && !self.validOptions.video) {
            $(self.$contentFile).addClass("bz-file-upload__container--hidden");
        }

        // Limit of files is reached then upload control is disabled
        if (self.isMaxFilesReached() || (!self.isNative && (!bizagi.util.media.fileAPISupported() || self.isOfflineForm))) {
            $(this.$contentFile).addClass("bz-file-upload__container--hidden");
        }
    },

    /**
     * Build add params to send to the server
     * @param {} dataFile
     */
    buildAddFileParams: function (dataFile) {
        var self = this;
        var prefix = self.Class.BA_CONTEXT_PARAMETER_PREFIX;

        // Get form data for POSTing
        var formData = new FormData();
        var form = self.getFormContainer();

        // Build add params to send to the server
        formData.append(prefix + "xpath", self.getUploadXpath());
        formData.append(prefix + "idRender", self.properties.id);
        formData.append(prefix + "xpathContext", self.properties.xpathContext);
        formData.append(prefix + self.Class.BA_PAGE_CACHE, self.properties.idPageCache);
        formData.append(self.Class.BA_ACTION_PARAMETER_PREFIX + "sessionId", form.properties.sessionId);
        formData.append(self.properties.xpath, dataFile);

        if (self.properties.contexttype)
            formData.append(prefix + "contexttype", self.properties.contexttype);

        try {
            if (typeof (BIZAGI_SESSION_NAME) !== "undefined") {
                formData.append(BIZAGI_SESSION_NAME, form.properties.sessionId);
            } else {
                formData.append("JSESSIONID", form.properties.sessionId);
            }
        } catch (e) {
            formData.append("JSESSIONID", form.properties.sessionId);
        }

        return formData;
    },

    /**
     * Process an upload file into the server
     * @param {} dataFile
     * @returns {}
     */
    processUploadFile: function (dataFile) {
        var data = {
            url: this.properties.addUrl,
            formData: this.buildAddFileParams(dataFile)
        };

        return this.dataService.processUploadFile(data);
    },

    /**
     * Remove data of file upload
     */
    clearDisplayValue: function () {
        var self = this;
        var items = $(".bz-files", self.getControl());

        $(".bz-file", items).each(function (index, item) {
            item = $(item);
            var file = item.data();

            self.removeFile(item, file.id);
        });
    },

    /**
     * Add upload options
     */
    getUploadChoices: function () {
        var self = this;
        var itemsUpload = [];

        // Take photo, Choose photo
        if (self.validOptions.image) {
            itemsUpload.push({
                "guid": 1,
                "displayName": bizagi.localization.getResource("workportal-actionsheet-upload-take-photo")
            });
            itemsUpload.push({
                "guid": 2,
                "displayName": bizagi.localization.getResource("workportal-actionsheet-upload-choose-photo")
            });
        }

        // Take video
        if (self.validOptions.video) {
            itemsUpload.push({
                "guid": 3,
                "displayName": bizagi.localization.getResource("workportal-actionsheet-upload-take-video")
            });
        }
        
        // Stores the upload options        
        return [].concat(itemsUpload);
    },

    /**
     * Call the form failhandler with an specific error
     * @param {} error
     * @returns {}
     */
    executeFormFailHandler: function (error) {
        var errorResponse = "";

        if (error && error.responseText) {
            errorResponse = error.responseText;
        } else {
            errorResponse = bizagi.localization.getResource("workportal-general-error");
        }

        this.getFormContainer().failHandler(errorResponse);
    },

    /**
     * Method to add new file from native or hybrid environment
     */
    chooseFile: function () {
        if (this.isNative) {
            this.$nativeFile.click();
        } else if (bizagi.util.media.fileAPISupported() && !this.isOfflineForm) {
            this.$inputFile.click();
        }
    },

    /**
     * Method to add new file
     */
    addFile: function () {
        var self = this;
        var dataFile = this.$inputFile[0].files[0];
        var extensions = self.Class.EXTENSIONSIMG.concat(self.Class.EXTENSIONSFILE, self.Class.EXTENSIONSVIDEO);

        dataFile.fullPath = this.$inputFile[0].value;
        bizagi.loading.start();

        if (bizagi.util.media.checkMaxSizeFile(dataFile, self.properties) && bizagi.util.media.checkFileTypes(dataFile, self.properties, extensions)) {

            $.when(self.processUploadFile(dataFile))
                .then(function (result) {
                    self.onUploadFileCompleted(JSON.parse(decodeURIComponent(result)));
                }).fail(function (error) {
                self.executeFormFailHandler(error);
            }).always(function () {
                bizagi.loading.stop();
            });
        } else {
            bizagi.loading.stop();
        }
    },

    /**
     * Method to remove item
     * @param item
     * @param id
     */
    removeFile: function (item, id) {
        var self = this;

        $.when(self.deleteUploadItem(item, id)).done(function () {
            // Remove item
            item.hide();
            item.remove();

            // Update value
            self.setValue(self.files, false);

            // Check maxFiles
            self.checkMaxFiles();

            // Trigger change
            self.triggerRenderChange();
        });
    },

    /**
     * Template for upload items
     */
    getTemplateItemName: function () {
        return "upload-item";
    },

    getElementsToStylize: function(context){
        return $(".bz-input-icon__input", context);
    }

});