/*
 *   Name: BizAgi Render Layout Image Class
 *   Author: Ricardo Prez
 *   Comments:
 *   -   This script will define basic stuff for non editable image control inside templates
 *   -   This control is based on the image no flash render control
 */

bizagi.rendering.smartphone.layoutRender.extend("bizagi.rendering.smartphone.layoutImage", bizagi.rendering.layoutImage.prototype);

bizagi.rendering.smartphone.layoutImage.extend("bizagi.rendering.smartphone.layoutImage", {
}, {
    
    renderControl: function () {
        var self = this;
        var templateUri = self.renderFactory.getTemplate("layout-image");
        self.control = self.getControl();
        return $.fasttmpl(templateUri, {});
    },

    postRender: function () {
        var self = this;
        var control = self.getControl();

        if (self.value && typeof self.value.value !== "undefined" && self.value.value !== "" && self.value.value !== null) {
            //Get image of customer in binary format
            var url = self.getImageUri();
            $.ajax({
                url: url,
                type: "GET",
                dataType: "binary",
                processData: false,
                success: function (blob) {

                    if (typeof blob == "string") {
                        var xhr = arguments[2];
                        var contentType = xhr.getResponseHeader('Content-type');
                        blob = bizagi.util.media.byteArrayToBlob(bizagi.util.media.stringToByteArray(blob), contentType);
                    }

                    var urlCreator = window.URL || window.webkitURL;
                    var imageUrl = urlCreator.createObjectURL(blob);

                    $(control).find('img').attr("src", imageUrl);
                }
            });
        }
    },
    
    /**
     * Gets the url to get the array image
     */
    getImageUri: function () {
        var self = this;

        var map = {
            entity: self.value.guid,
            surrogateKey: self.value.surrogateKey,
            xpath: self.properties.xpath,
            width: 96,
            height: 96,
            hash: self.value.value
        };

        return self.dataService.serviceLocator.getUrl("render-entity-layout-imageByteArray").replaceMultiple(map);
    },
    /**
     *   Sets the value in the rendered control
     */
    getDisplayValue: function () {
        var self = this;
        return [self.value];
    }
});