/**
 *   Name: Bizagi Smartphone Render Form Link Extension
 *   Author: Bizagi UI Forms
 *   Comments:
 *   -   This script will redefine the link render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.formLink", {
    BA_ACTION_PARAMETER_PREFIX: "p_",
    BA_CONTEXT_PARAMETER_PREFIX: "h_",
    BA_PAGE_CACHE: "pageCacheId"
}, bizagi.rendering.formLink.prototype);
// Extends itself
bizagi.rendering.smartphone.formLink.extend("bizagi.rendering.smartphone.formLink", {}, {

    init: function (params) {
        var self = this;

        // Call base
        self._super(params);

        params.context = params.context || "workflow";

        var paramsRender = $.extend({}, this._params, {
            "proxyPrefix": this.dataService.serviceLocator.proxyPrefix,
            "context": this.dataService.serviceLocator.context
        });

        this.serviceLocator = new bizagi.render.services.context(paramsRender);
    },

    /**
     * Update or init the element data
     * @param data
     */
    initializeData: function (data) {
        var self = this;

        self._super(data);

        var properties = self.properties;

        self.originalElement = self.element;
        properties.orgiginalDisplayType = properties.displayType;
        properties.displayType = "normal";

        self._sendRelation = self.submitEditRequest;
        self._getData = self.dataService.getFormData;
        self._isRefreshForm = false;
    },

    /**
     * Method to customize each render after processed
     */
    postRender: function () {
        var self = this;
        var properties = self.properties;

        self._super();

        self._params = {
            idRender: properties.id,
            xpathContext: self.getFormLinkXpathContext(),
            idPageCache: properties.idPageCache,
            recordXPath: self.getFormLinkXpath(),
            requestedForm: "linkform",
            editable: properties.editable,
            url: properties.editPage
        };

    },

    /**
     * Method to customize each render after processed in read-only mode
     */
    postRenderReadOnly: function () {
        // Execute the same as post-render
        this.postRender();
    },

    /**
     * Para la grilla lo que se debe hacer es sobreescribir o crear los metodos params, sendrelation,getData
     * @returns {*}
     */
    renderEdition: function () {
        var self = this;
        var properties = self.properties;

        self.container = self.getParams().navigation.createRenderContainer({title: properties.displayName});
        self.inputEdition = self.container.element;

        if (self.properties.hasentryrule) {
            var form = self.getFormContainer();

            $.when(form.saveForm()).done(function () {
                self.resolveCheckpointRequest().done(function () {
                    return self.openLinkRule("entryrule");
                });
            });
        } else {
            return self.openLink();
        }
    },

    /**
     * Open link display from inside a dialog
     */
    openLink: function () {
        var self = this;

        return $.when(self._sendRelation.apply(self)).done(function () {
            bizagi.loading.start();

            $.when(self.paintFormLink()).done(function () {
                bizagi.loading.stop();
            }).fail(function () {
                //Executes Rollback action
                bizagi.loading.start();
                self.rollbackForm();
            }).always(function () {
                bizagi.loading.stop();
            });
        }).fail(function (error) {
            var error_obj = bizagi.util.getErrorFormatted(error);
            var message = error_obj.message || bizagi.localization.getResource("workportal-general-error");
            var form = self.getFormContainer();

            if (typeof (form) !== "undefined") {
                form.addValidationMessage(message);
            }
        }).always(function () {
            bizagi.loading.stop();
        });
    },

    openLinkRule: function (property) {
        var self = this;
        var def = new $.Deferred();

        bizagi.loading.start();

        self.getPropertyValue(property, {})
            .done(function (dataRuleResp) {
                if (dataRuleResp.messages) {
                    var message = dataRuleResp.messages[0];
                    var form = self.getFormContainer();

                    form.validationController.showErrorMessage(message);
                    def.reject();
                } else {
                    if (property === "entryrule") {
                        self.openLink();
                    }
                }
                def.resolve();
            }).fail(function () {
            def.reject();
        }).always(function () {
            bizagi.loading.stop();
        });

        return def.promise();
    },

    getPropertyValue: function (propertyName, params) {
        var self = this;
        var properties = self.properties;
        var defer = new $.Deferred();

        self.dataService.multiaction().getPropertyData({
            url: properties.dataUrl,
            xpath: properties.xpath,
            idRender: properties.id,
            xpathContext: properties.xpathContext,
            idPageCache: properties.idPageCache,
            property: propertyName,
            extra: params,
            dataType: params ? params.dataType : null
        }).done(function (data) {
            if (data.type === "error") {
                // Workflow engine error, we gotta show it with error
                var form = self.getFormContainer();

                form.validationController.showErrorMessage(data.message);
                defer.reject(data);
            } else {
                defer.resolve(data);
            }
        }).fail(function (jqXHR, textStatus, errorThrown) {
            // Add error messages
            var form = self.getFormContainer();

            form.validationController.showErrorMessage(jqXHR.message);

            defer.reject(jqXHR, textStatus, errorThrown);
        });

        return defer.promise();
    },

    paintFormLink: function () {
        var self = this;
        var deferred = $.Deferred();

        $.extend(self._params, {"proxyPrefix": self.serviceLocator.proxyPrefix});

        $.when(self._getData.call(self, self._params)).pipe(function (data) {
            if (self._params.recordXPath) {
                data.form.properties.recordXpath = self._params.recordXPath;
            }

            if (self._params.editable === false) {
                data.form.properties.editable = false;
            }

            // Grid
            if (self._params.hasOwnProperty("withAddForm") && self._params.withAddForm) {
                if (!data.form.elements || (data.form.elements && data.form.elements.length === 0)) {
                    self._params.hideSaveButton = true;
                }
            }

            /**
             * This creation of form on every action like refresh or save
             * is driving nuts the listeners in native apps
             */
            self.form = self.renderFactory.getContainer({
                type: "form",
                data: data.form,
                navigation: self.getParams().navigation,
                wiew_container: self.container
            });

            self.form.params = self.getFormContainer().getParams();

            self.form.buttons = [
                $.extend(
                    self.form.buttons[0] || {},
                    {
                        "caption": bizagi.localization.getResource("render-form-dialog-box-save"),
                        "actions": ["submitData", "refresh"],
                        "submitData": true,
                        "refresh": true,
                        "ordinal": 0,
                        "action": "save",
                        "save": true,
                        "style": "",
                        // This callback avoid to call the default processButton in the form
                        callback: function () {
                            // When the data is saved then
                            return self.saveRecord().then(function () {
                                self.container.destroy();
                            }).fail(function (error) {
                                if (error && error.error_code && error.error_code == 1) {
                                    //if doesn't support commit, rollback, etc.
                                    $.when(self.actionSave(error.data)).then(function () {
                                        self.container.destroy();
                                    });
                                } else if (error && error.type === "validationMessages") {
                                    self.form.addValidationMessage(error.messages);
                                } else if (error && error.responseText) {
                                    var error_obj = bizagi.util.getErrorFormatted(error);
                                    var message = error_obj && error_obj.message ? error_obj.message : (error_obj || bizagi.localization.getResource("workportal-general-error"));

                                    if (self.hasSessionFormException(error_obj)) {
                                        self.goToStartPage(error_obj);
                                    } else {
                                        bizagi.showMessageBox(message, "Error");
                                    }
                                }
                            });
                        }
                    }),
                $.extend(
                    self.form.buttons[1] || {},
                    {
                        "caption": bizagi.localization.getResource("render-form-dialog-box-close"),
                        "actions": ["submitData", "refresh"],
                        "submitData": true,
                        "refresh": true,
                        "ordinal": 1,
                        "action": "back",
                        "save": true,
                        "style": "",
                        //This callback avoid to call the default processButton in the form
                        callback: function () {
                            self.container.destroy();
                            deferred.reject();
                        }
                    })
            ];

            $.extend(self.form, {
                processButtons: function () {
                    //not send information set in memory and save
                    $.each(self.form.buttons, function (index, element) {
                        switch (element["ordinal"]) {
                            case 1: //next button will behave as a cancel button
                                $(self.form.getButtons()[index]).click(function () {
                                    self.container.destroy();
                                    deferred.reject();
                                });

                                self.form.getButtons()[index].innerHTML = bizagi.localization.getResource("render-form-dialog-box-close");

                                self.form.buttons[index].caption = bizagi.localization.getResource("render-form-dialog-box-close");
                                self.form.buttons[index].action = "back";
                                break;

                            default:
                                $(self.form.getButtons()[index]).click(function () {
                                    if (self._params.hideSaveButton) {
                                        $(self.form.getButtons()[index]).hide();
                                    }

                                    return self.saveRecord().then(function () {
                                        self.container.destroy();
                                    }).fail(function (error) {
                                        if (error && error.error_code && error.error_code == 1) {
                                            //if doesn't support commit, rollback, etc.
                                            $.when(self.actionSave(error.data)).then(function () {
                                                self.container.destroy();
                                            });
                                        } else {
                                            var error_obj = bizagi.util.getErrorFormatted(error);
                                            var message = error_obj && error_obj.message ? error_obj.message : (error_obj || bizagi.localization.getResource("workportal-general-error"));

                                            bizagi.showMessageBox(message, "Error");
                                        }
                                    });
                                });
                                break;
                        }
                    });
                }
            });

            return self.form.render();

        }, function (error) {
            var error_obj = bizagi.util.getErrorFormatted(error);
            var message = error_obj.message || bizagi.localization.getResource("workportal-general-error");
            var form = self.getFormContainer();

            if (typeof (form) !== "undefined") {
                form.addValidationMessage(message);
            }

            deferred.reject();
        }).done(function (element) {
            self.inputEdition.html(element);

            if (!self._isRefreshForm) {
                self.getParams().navigation.navigate(self.container.id);
            }

            self.container.setNavigationButtons(self.form);

            // Publish an event to check if the form has been set in the DOM
            self.form.triggerHandler("ondomincluded");

            // Attach a refresh handler to recreate all the form
            self.form.bind("refresh", function () {
                //TODO the parent who call this class implement redraw. see smartphone.grid _InternalRenderLinkForm
                self.inputEdition.empty();
                self._isRefreshForm = true;

                //Adds the events to capture the
                $.when(self.paintFormLink()).done(function () {
                }).fail(function () {
                    self.submitRollbackRequest();
                });
            });

            // Binds the view close event, and send the RollBack
            self.container.element.bind("close", function () {
                self.rollbackForm();
            });

        }).always(function () {
            bizagi.loading.stop();
        });

        return deferred.promise();
    },

    rollbackForm: function () {
        var self = this;
        var form = self.form || self.getFormContainer();

        $.when(self.submitRollbackRequest({idPageCache: form.idPageCache})).done(function () {
            // Sends additional rollback request to delete checkpoint from entry rule
            if (self.properties.hasentryrule) {
                self.submitRollbackRequest({idPageCache: form.idPageCache});
            }
        });
    },

    saveRecord: function () {
        var self = this;
        var deferred = $.Deferred();
        var properties = self.properties;

        if (self.form.validateForm()) {
            var data = {};
            self.form.collectRenderValues(data);
            // Add page cache for this form
            data.idPageCache = self.form.getPageCache();

            $.when(self.submitSaveRequest(data)).done(function (result) {
                try {
                    if(self.properties.hasExclusiveCheck && self.addExclusiveChange){
                        self.addExclusiveChange(self.properties.idAsigned, data, self.parent);
                    }
                    var executeSubmitOnChange = true;
                    if (result && result.type === "validationMessages") {
                        executeSubmitOnChange = false;
                        deferred.reject(result);
                    }
                    if (properties.hasexitrule) {
                        self.closeExitRule().done(function () {
                            if (executeSubmitOnChange) {
                                self.resolveSubmitCommitRequest().then(function () {
                                    self.submitOnChange({}).then(function () {
                                        deferred.resolve(result);
                                        if (properties.hasentryrule) {
                                            self.resolveSubmitCommitRequest();
                                        }
                                    });
                                });
                            } else {
                                deferred.reject();
                            }
                        }).fail(function () {
                            deferred.reject();
                        });
                    } else if (executeSubmitOnChange) {
                        self.resolveSubmitCommitRequest().then(function () {
                            if (properties.hasentryrule) {
                                self.resolveSubmitCommitRequest().done(function () {
                                    self.submitOnChange({}).then(function () {
                                        deferred.resolve(result);
                                    });
                                });
                            } else {
                                self.submitOnChange({}).then(function () {
                                    deferred.resolve(result);
                                });
                            }
                        });
                    } else {
                        deferred.reject();
                    }
                } catch (ex) {
                    deferred.reject();
                }
            }).fail(function (error) {
                var err = JSON.parse(error.responseText || '{"message":"","code":""}');

                if (err.code === "UNKNOWN_ERROR" && err.message.indexOf("Requested value") !== -1
                    && err.message.indexOf("was not found") !== -1) {
                    deferred.reject({error_code: 1, data: data});
                } else {
                    deferred.reject(error);
                }
            });
        } else {
            deferred.reject();
        }

        return deferred.promise();
    },

    submitSaveRequest: function (data) {
        var self = this;
        var properties = self.properties;

        // Don't send request when the link is not editable
        if (properties.editable === false && !properties.value) {
            return null;
        }

        var xpath = self.getFormLinkXpath();
        var xpathContext = xpath;

        if (properties.xpathContext !== undefined && properties.xpathContext !== "") {
            xpathContext = xpath ? properties.xpathContext + "." + xpath : properties.xpathContext;
        }

        // SaveGridRecord
        return self.dataService.saveGridRecord({
            url: properties.saveUrl,
            idRender: properties.id,
            xpathContext: xpathContext,
            submitData: data
        });
    },

    resolveSubmitCommitRequest: function () {
        var def = new $.Deferred();

        this.submitCommitRequest().done(function () {
            def.resolve();
        }).fail(function (error) {
            var err = JSON.parse(error.responseText || "{message:'', code: ''}");
            if (err.code === "UNKNOWN_ERROR") {
                def.resolve();
            } else {
                def.reject();
            }
        });

        return def.promise();
    },

    resolveCheckpointRequest: function () {
        var def = new $.Deferred();

        this.submitCheckpointRequest().done(function () {
            def.resolve();
        }).fail(function (error) {
            var err = JSON.parse(error.responseText || "{message:'', code: ''}");

            if (err.code === "UNKNOWN_ERROR" && err.message.indexOf("Requested value") !== -1
                && err.message.indexOf("was not found") !== -1) {
                def.resolve();
            } else {
                def.reject();
            }
        });

        return def.promise();
    },

    closeExitRule: function () {
        var def = new $.Deferred();

        if (this.properties.hasexitrule) {
            $.when(this.openLinkRule("exitrule")).done(function (result) {
                def.resolve(result);
            }).fail(function () {
                def.reject();
            });
        } else {
            def.resolve();
        }

        return def.promise();
    },

    getFormLinkXpathContext: function () {
        var xpathContext = this.properties.xpathContext;
        var parentXpathContext = this.getXpathContext();

        if (parentXpathContext && xpathContext) {
            return xpathContext.indexOf(parentXpathContext) === 0 ? xpathContext : parentXpathContext + "." + xpathContext;
        } else {
            return xpathContext;
        }
    },

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers
     */
    configureHandlers: function () {
        this.attachEvents();
    },

    /**
     * Template method to implement in each device to customize the render's behaviour
     * to add handlers when the control is readonly
     */
    configureHandlersReadOnly: function () {
        this.attachEvents();
    },

    /**
     * Configure event handlers
     */
    attachEvents: function () {
        var self = this;
        var $control = self.getControl();

        $("[data-action=open]", $control).on("click", function () {
            bizagi.loading.start();

            $.when(self.renderEdition()).always(function () {
                bizagi.loading.stop();
            });
        });
    },

    processLayout: function (isLabelFormat) {
        if (!isLabelFormat) {
            this._super(isLabelFormat, this.properties.textFormat);
        } else {
            if (typeof this.properties.textFormat.background !== "undefined") {
                this._super(isLabelFormat, {background: this.properties.textFormat.background});
            }
        }
    },

    getElementsToStylize: function ($context) {
        return $(".bz-link", $context);
    },
    /**
     * Idle timer expired exception
     * @param error
     * @returns {boolean}
     */
    hasSessionFormException: function (error) {
        return typeof (error) !== "undefined" && error.code === "AUTH_ERROR";
    },

    /**
     * Navigate to main page
     * @param error
     */
    goToStartPage: function (error) {
        var navigation = this.getParams().navigation;
        if (navigation) {
            navigation.goToStartPage(error);
        }
    }

});