/**
 *   Name: Bizagi Smartphone Render Yes-No Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the multiSelect render class to adjust to smartphone devices
 */

// Extends from render tablet base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.multiSelect", bizagi.rendering.multiSelect.prototype);
// Extends itself
bizagi.rendering.smartphone.multiSelect.extend("bizagi.rendering.smartphone.multiSelect", {
    MORE_OPTION_WIDTH: 40,
    SEARCH_MIN_LENGTH: 1
}, {


    renderReadOnly: function () {
        var self = this;
        var template = self.renderFactory.getTemplate("searchList.readonly");

        // Render template
        var content = $.fasttmpl(template, {
            items: self.properties.value || {},
            editable: self.properties.editable || true
        }, {
            formatItem: self.formatItem
        });

        return content;
    },

    /**
     * Template method to implement in each device to customize each render after processed
     */
    postRender: function () {
        var self = this;

        if (this.isExecutionMode()) {
            this.getData().done(function (data) {
                self.properties.data = data;
            });
        }

        // Call base
        this._super();
    },

    configureHandlers: function () {
        var self = this;
        var $control = self.getControl();
        $("[data-action=modal]", $control).bind("click", function () {
            self.openModalView();
        });
    },

    /**
     * Sets the value and display, avoid to include setValue in this method.
     */
    setDisplayValue: function (value) {
        var self = this;
        this._super(value);

        if (value !== undefined) {
            $(".bz-input-icon__input", this.getControl()).html(value.map(function (el) {
                return self.formatItem(el.value);
            }).join(", "));
        }
    },
    /**
     * Opens the modal view
     */
    openModalView: function () {
        var self = this;
        self.getData().done(function (result) {
            var template_content = self.renderFactory.getTemplate('multiselect-list');
            var value = self.getValue();

            if (!$.isArray(value)) {
                value = typeof (value) === "string" ? JSON.parse(value).value : value.value;
            }

            var data = {
                values: result,
                isChecked: function (id) {
                    return self.isChecked(value, id);
                }
            };

            var content = $.fasttmpl(template_content, data, {
                formatItem: self.formatItem
            });

            var template_footer = self.renderFactory.getTemplate("modal-footer-done");
            var footer = $.fasttmpl(template_footer, {});

            self.getControl().mobileView({
                customHeader: $.fasttmpl(self.renderFactory.getTemplate("modal-header-search"), {}),
                // displayName: self.properties.displayName || "",
                orientation: self.properties.orientation || "",
                contentTemplate: content,
                footerTemplate: footer,
                transition: "slideIn:up",
                close: function () {
                    self.triggerGlobalHandler("modalViewDidDisappear", {});
                },
                afterOpenAnimation: function (event) {
                    $(".bz-modal-view__search", event.view.header).focus();
                },
                open: function () {
                    self.triggerGlobalHandler("closeModalValidationMessage", {});
                    self.triggerGlobalHandler("modalViewDidAppear", {});
                },
                configureActions: function (modalView) {
                    self.configureModalViewHandlers(modalView);
                }
            });
        });
    },

    /**
     * Configure the modalView Handlers for the new combo control.
     * */
    configureModalViewHandlers: function (modalView) {
        var self = this;

        var $listContainer = $(".bz-checkbox-list li", modalView.content);
        //clear button
        var $clearButton = $(".bz-modal-view__clear", modalView.header);
        // Suggest input
        var $suggestInput = $(".bz-modal-view__search", modalView.header);

        $suggestInput.on("change keyup", function () {
            if (this.value == "") {
                $clearButton.removeClass("bz-modal-view__clear--visible");
            } else {
                $clearButton.addClass("bz-modal-view__clear--visible");
            }
            self.filterList(this.value, $listContainer);
        }).on("focus", function () {
            modalView.header.addClass("bz-modal-view__header--active");
        }).on("blur", function () {
            modalView.header.removeClass("bz-modal-view__header--active");
        });

        //Clear the search
        $clearButton.on("click", function () {
            $suggestInput.val("").trigger("change");
        });

        $("[data-action=close]", modalView.header).on("click", function () {
            modalView.close();
        });

        $(modalView.footer).on("click", "[data-action=save]", function (evt) {
            // Create a temporal array
            var newList = [];

            $.each.call(this, $listContainer, function (index, item) {
                var check = item.querySelector("input");

                if (check.checked) {
                    var id = check.getAttribute("data-id");
                    var element = this.findItemById(id);
                    var value = element.value;
                    newList.push({id: id, value: value});
                }
            }.bind(self));

            self.setDisplayValue(newList);

            modalView.close();
        });
    }
});
