/**
 *   Name: Bizagi Smartphone Render Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the render class to adjust to smartphone devices
 */

// Extends from render base
bizagi.rendering.render.extend("bizagi.rendering.smartphone.render", bizagi.rendering.render.base.prototype);
// Extends itself
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.render", {}, {

    /**
     * Update or init the element data
     * @param data
     */
    initializeData: function (data) {
        // Call base
        this._super(data);

        var formContainer = this.getFormContainer();
        this.formParams = formContainer.getParams();
    },

    /**
     * Override internal rendering in order to apply new smartphone layout
     */
    internalPostRender: function (element) {
        this._super(element);

        this.changeDisplayOption();
    },

    /**
     * Template method to implement in each device to customize each render after processed
     */
    postRender: function () {
        // Call base
        this._super();
    },

    /**
     * Returns the label element usually to apply styles
     */
    getLabel: function() {
        if (!this.label)
            this.label = $(".bz-form-label label", this.element || this.observableElement);

        return this.label;
    },

    getContainerRender: function () {
        return $(this.element);
    },

    /**
     * Return the right align class
     */
    getAlignClass: function (alignType) {
        return alignType;
    },

    /**
     * Template method to get the control element
     */
    getControl: function () {
        if (!this.control || this.control.length === 0)
            this.control = $(".bz-control", this.element || this.observableElement);

        if (!this.control || this.control.length === 0) return null;

        return this.control;
    },

    /**
     * Template method to get the control filler element
     */
    getControlFiller: function () {
        if (!this.controlFiller)
            this.controlFiller = $(".bz-form-item__control", this.element || this.observableElement);

        return this.controlFiller;
    },

    getContainerMessage: function () {
        if (!this.containerMessage)
            this.containerMessage = $(".bz-form-message__text", this.element || this.observableElement);

        return this.containerMessage;
    },

    getColumnContainerMessage: function () {
        if (!this.containerMessage)
            this.containerMessage = $(".bz-form-cell-message__text", this.element || this.observableElement);

        return this.containerMessage;
    },

    /**
     * Customizes render label align
     */
    changeLabelAlign: function (alignType) {
    },

    /**
     * Customizes render value align
     */
    changeValueAlign: function (alignType) {
    },

    /**
     * Customizes render label and value width
     * @returns {}
     */
    customizeRenderWidth: function () {
    },

    /**
     * Sends all the info to the server then refreshes the form. Returns a deferred
     */
    submitOnChange: function (data, bRefreshForm, noCollect) {
        var self = this;
        var defer = new $.Deferred();

        // TODO: Review loader for smartphone
        bizagi.util.smartphone.startLoading();

        $.when(self.isReadyToSave()).done(function () {
            $.when(self.internalSubmitOnChange(data, bRefreshForm, noCollect))
                .done(function () {
                    defer.resolve();
                }).fail(function () {
                defer.reject();
            }).always(function () {
                bizagi.util.smartphone.stopLoading();
            });
        });

        return defer.promise();
    },

    /**
     * Adds a validation message to the render
     */
    setValidationMessage: function (message) {
        // Call base
        this._super(message);
        var dataType = this.properties.type;
        var isGridControl = (bizagi.util.normalizeType(dataType) === "grid");
        var isColumn = (this.column && /column/.test(dataType));
        var $message = isColumn ? this.getColumnContainerMessage() : this.getContainerMessage();

        if (message && message.length > 0) {
            message = message.replace(/<\w+>([^<]*)<\/\w+>/g, "$1");

            $message.attr("data-error", message);
            $message.text(message);

            isGridControl ? this.element.addClass("bz-form-item-grid--error") : this.element.addClass("bz-form-item--error");
        } else {
            $message.text("");
            isGridControl ? this.element.removeClass("bz-form-item-grid--error") : this.element.removeClass("bz-form-item--error");
        }
    },

    /**
     * Sets the value in the rendered control
     */
    setDisplayValue: function (value) {
        var self = this;
        var control = self.getControl();

        // Set internal value
        self.setValue(value, false);

        if (!self.properties.editable && self.properties.display !== "check") {
            // Render as simple value
            var displayValue = self.getDisplayValue(value);

            if (typeof (displayValue) == "string" || typeof (displayValue) == "number") {
                $(control).html("<label class='bz-form-label'>" + displayValue + "</label>");
            }
        }
    },

    actionSave: function () {
        this.setValue(this.inputEdition.val(), false);
    },

    configureLabel: function () {
        return this.properties.displayName;
    },

    /**
     * Starts waiting signal for async stuff
     */
    startLoading: function () {
    },

    /**
     * Ends waiting for async stuff
     */
    endLoading: function () {
    },

    /**
     * In mobiles, don't apply styles to icon's elements
     * @param context
     * @returns {jQuery|HTMLElement}
     */
    getElementsToStylize: function (context) {
        return $(".bz-input", context);
    },

    /**
     * Return the right display option class
     * @param displayType
     * @returns {string}
     */
    getDisplayOptionClass: function (displayType) {
        if (displayType === "reversed") {
            return "bz-form-item--display-reversed";
        } else if (displayType === "label") {
            return "bz-form-item--display-label";
        } else if (displayType === "value") {
            return "bz-form-item--display-value";
        }
    },

    /**
     * Return the label
     */
    getFormattingLabel: function () {
        // TODO: Despues de cambiar a genome desktop, validar se solo se deja este metodo en la base
        return this.getLabel();
    },

    /**
     * Sets the value in the rendered control
     */
    clearDisplayValue: function () {
        var self = this;
        var control = self.getControl();

        var input = control.find("input").filter(function (index, ele) {
            return ele.type !== "file" && ele.type !== "radio" && ele.type !== "checkbox"
        });

        if (input.length > 0) {
            input.val(self.getValue());
        } else {
            self.setDisplayValue("");
        }
    },

    getFormParams: function () {
        return this.formParams || {};
    },

    /**
     * Transform the display type when using RTL orientation
     */
    getRTLDisplayType: function (displayType) {
        return displayType;
    },

    /**
     * Changes the render visibility
     * @param argument
     */
    changeVisibility: function (argument) {
        // Update properties
        this.properties.visible = argument;

        // Hide - show the render
        if (bizagi.util.parseBoolean(argument)) {
            if (this.properties.type !== "hidden") {
                this.element.show();
                this.element.css("display", "");
            }
        } else if (this.element) {
            this.element.hide();
        }
    },

    /**
     *  Changes the render required
     * @param argument
     */
    changeRequired: function (argument) {
        // Update properties
        this.properties.required = argument;

        if (bizagi.util.parseBoolean(argument) && this.properties.editable) {
            this.element.addClass("bz-form-item--required");
        } else {
            this.element.removeClass("bz-form-item--required");
        }

        // Perform validations again to check if the form is valid after this change
        this.triggerRenderValidate();
    },

    /**
     * This method just make a toggle of red label indicator
     * @argument {boolean} argument
     */
    changeRequiredLabel: function (argument) {
        // Prevent take the base
    },

    /**
     * Change cell editability and attach bindings
     * @param argument
     * @param key
     * @param xpath
     */
    changeEditabilityCellControl: function (argument, key, xpath) {
        this._super(argument, key, xpath);
    }

});
