/*
*   Name: BizAgi Render Layout Label Class
*   Author: Ricardo Prez
*   Comments:
*   -   This script will define basic stuff for labels inside templates
*/

bizagi.rendering.smartphone.layoutRender.extend("bizagi.rendering.smartphone.layoutLabel", bizagi.rendering.layoutLabel.prototype);

bizagi.rendering.smartphone.layoutLabel.extend("bizagi.rendering.smartphone.layoutLabel", {

    setValue: function (val, triggerEvents) {
        var self = this;
        self.properties.displayName = val;
        this._super(val, triggerEvents);
        var control = self.getControl();
        if (control) {
            $(".ui-bizagi-render-label", control).text(val);
        }
    }
});
