/**
 *   Name: Bizagi Smartphone Render fileprint Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the fileprint render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.fileprint", bizagi.rendering.fileprint.prototype);
// Extends itself
bizagi.rendering.smartphone.fileprint.extend("bizagi.rendering.smartphone.fileprint", {}, {

    /**
     * Template method to implement in each device to customize each render after processed
     */
    postRender: function () {
        this.getControl().parent().hide();
    }

});
