/**
 *   Name: Bizagi Smartphone Render upload Extension
 *   Author: Mobile SWAT Team
 */

// Extends itself
bizagi.rendering.smartphone.upload.extend("bizagi.rendering.smartphone.upload.offline", {
    QUALITY_PICTURE: 50,
    LIMIT: 1, // The maximum number of media in the device user can record in a single capture operation.
    EXTENSIONSIMG: ["image/jpeg", "jpeg", "image", "png", "jpg", "bmp"],
    EXTENSIONSFILE: ["doc", "docx", "xls", "xlsx", "ppt", "pptx", "pdf", "zip", "rar", "txt"],
    EXTENSIONSVIDEO: ["video/quicktime", "quicktime", "qt", "mov"],
    EXTENSIONSAUDIO: ["audio/wav", "audio", "wav", "mp3"]
}, {

    /**
     * Update or init the element data
     * @param {} data
     */
    initializeData: function (data) {
        // Call base
        var self = this;
        this._super(data);

        var maxFileSizeOfProperty = Number(this.properties.maxSize) || 1048576;
        var maxFileSizeOfUser = typeof (BIZAGI_SETTINGS) !== "undefined" && typeof (BIZAGI_SETTINGS.UploadMaxFileSize) !== "undefined" ? Number(BIZAGI_SETTINGS.UploadMaxFileSize) : 1048576;

        this.properties.maxSize = maxFileSizeOfProperty < maxFileSizeOfUser ? maxFileSizeOfProperty : maxFileSizeOfUser;

        this.updateFilesCount();
    },
    /**
     * Method to render items
     * @returns {*|jQuery}
     */
    renderItem: function (template, data) {
        var self = this;
        var items = "";

        // Render template
        var html = $.fasttmpl(template, data);

        // Render current children
        for (var i = 0; i < self.files.length; i++) {
            var file = self.files[i];
            if(file.status && file.status !== "deleted" && file.status !== "new-deleted"){
                var item = self.renderUploadItem(file, i);
                items += item;
            }
        }

        html = self.replaceFilesHtml(html, items);

        return html;
    },

    /**
     * Method to customize the render's behaviour to add handlers when the control is readonly
     */
    configureHandlersReadOnly: function () {
        var self = this;
        var $control = self.getControl();

        // Handler to download the file
        $("ul.bz-files", $control).on("click", ".bz-file .bz-file-name", function () {
            var item = $(this).parent(".bz-file");
            self.openOfflineFile(item);
        });
    },

    /**
     * Method to configure the actions with control
     * @return {}
     */
    attachEvents: function () {
        var self = this;
        var $control = self.getControl();

        // Attach all events
        this.$contentFile.off("click").on("click", function (evt) {
            if(self.isNative)
                self.$nativeFile.click();
            else
                self.$inputFile.click();
                self.$inputFile.off("change").on("change", function(){
                    self.handlerWebUploadFile();
                });
        });

        // Handler to download the file
        $("ul.bz-files", $control).on("click", ".bz-file .bz-file-name", function () {
            var item = $(this).parent(".bz-file");
            self.openOfflineFile(item);
        });

        // Attach delete option & Removes a file from the upload control
        $("ul.bz-files", $control).on("click", ".bz-file .bz-icn", function () {
            var item = $(this).parent(".bz-file");
            self.removeOfflineFile(item);
        });

        if (self.isNative) {
            // Upload files
            this.$nativeFile.off("click").actionSheet({
                actions: self.getUploadChoices(),
                actionClicked: function (action) {
                    // this.getBoundingClientRect()
                    self.handlerUploadFile(action.guid);
                },
                appear: function (action) {
                    self.triggerGlobalHandler("closeModalValidationMessage", {});
                    self.triggerGlobalHandler("modalViewDidAppear", {});
                },
                cancelClicked: function (action) {
                    self.triggerGlobalHandler("modalViewDidDisappear", {});
                }
            });
        }
    },

    //Only should be used for testing purposes
    handlerWebUploadFile: function(){
        var self = this;
        var dataFile = self.$inputFile[0].files[0];
        
        var fileId = bizagi.util.randomNumber();
        var fileName, mimeType;
        
        $.when(self.getBlobFromPath(dataFile))
        .then( function(blob){            
            bizagi.loading.start();
            mimeType = blob.type;
            fileName = blob.name;
            return self.saveOfflineFile(blob, fileId, mimeType);
        }).then( function(saveResponse){
            self.onUploadOfflineFileCompleted(fileName, mimeType, fileId);
        }).always( function(){
            bizagi.loading.stop();
        })
        self.$inputFile[0].value = "";
    },

    /**
     * Handler of upload image
     * @param {} ordinal
     * @returns {}
     */
    handlerUploadFile: function (ordinal) {
        var self = this;
        var actionSelected = ordinal || 0;
        switch (actionSelected) {
            case 2:
                navigator.camera.getPicture(function (dataImage) {
                    self.dataImage = dataImage;
                    var extensions = self.Class.EXTENSIONSIMG.concat(self.Class.EXTENSIONSVIDEO);

                    bizagi.loading.start();

                    $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties))
                        .then(function () {
                            //Gets the real properties for the file
                            return $.when(bizagi.util.media.getFileDataInfo(self.dataImage))
                                .then(function (fileData) {
                                    self.fileProperties = fileData;
                                    if (bizagi.util.media.checkFileTypes(fileData, self.properties, extensions)) {
                                        return self.saveImage(self.dataImage);
                                    }
                                });
                        }).always(function () {
                        bizagi.loading.stop();
                    });

                }, self.onFail.bind(self), {
                    quality: self.Class.QUALITY_PICTURE,
                    sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
                    correctOrientation: true
                });
                break;
            case 1:
                navigator.camera.getPicture(function (dataImage) {
                        bizagi.loading.start();

                        $.when(bizagi.util.media.checkMaxSize(dataImage, self.properties)).then(function () {
                            return $.when(bizagi.util.media.getFileDataInfo(dataImage)).then(function (fileData) {
                                self.fileProperties = fileData;
                                return self.saveImage(dataImage);
                            });
                        }).always(function () {
                            bizagi.loading.stop();
                        });
                    },
                    self.onFail.bind(self), {
                        quality: self.Class.QUALITY_PICTURE,
                        sourceType: navigator.camera.PictureSourceType.CAMERA,
                        mediaType: navigator.camera.MediaType.PICTURE,
                        destinationType: Camera.DestinationType.FILE_URI,
                        correctOrientation: true,
                        targetWidth: 1280,
                        targetHeight: 960
                    });
                break;
            default:
                console.log("The option is not supported!")
                break;
        }
    },

    /**
     * Add the interactions to the upload control items
     * @return {}
     */
    addInteractions: function () {
        var self = this;
        var control = self.getControl();

        // Attach delete option & Removes a file from the upload control
        $("ul.bz-files", control).on("click", ".bz-file .bz-file__delete", function () {
            var item = $(this).parent(".bz-file");
            self.removeOfflineFile(item);
        });
    },

    /**
     * Collect Data
     * @param {} renderValues
     * @returns {}
     */
    collectData: function (renderValues) {
        var self = this;
        if (self.properties.originalValue === self.files) {
            return;
        }
        if (self.filesCount > 0) {
            renderValues[self.properties.xpath] = self.files;
        } else if (self.properties.method === "delete") {
            renderValues[self.properties.xpath] = [];

            delete self.properties.method;
        }
    },

    /**
     * Send image using HTTP
     * @param {} dataImage
     */
    saveImage: function (dataImage) {
        var self = this;
        bizagi.loading.start();

        var fileId = bizagi.util.randomNumber();
        var fileName = self.fileProperties.name
        var mimeType;

        //TODO: SWAT Check if the file already exists
        $.when(self.getBlobFromPath(dataImage))
        .always( function(){
            bizagi.loading.stop();
        })
        .then( function(blob){
            mimeType = blob.type;
            fileName = blob.name;
            return self.saveOfflineFile(blob, fileId, mimeType);
        }).then( function(saveResponse){            
            self.onUploadOfflineFileCompleted(fileName, mimeType, fileId);            
        });
        self.$inputFile[0].value = "";
    },

    /**
     * Check allow extensions and define possible choices
     * Only support to image in offline capability
     * @returns {}
     */
    checkFileExtensions: function () {
        var self = this;
        var validExtensions = self.properties.validExtensions || "";

        self.validOptions = {video: false, image: false, file: false};

        // Preprocess the extensions string
        validExtensions = validExtensions.replace(/\s+/g, "").toLowerCase();

        if (typeof (validExtensions) === "undefined" || validExtensions === "") {
            self.validOptions.image = true;
        } else {
            validExtensions = validExtensions.split(";");

            if (validExtensions.length === 1 && validExtensions[0].indexOf("*.*") !== -1) {
                self.validOptions.image = true;
            } else {
                for (var i = 0, length = validExtensions.length; i < length; i++) {
                    validExtensions[i] = validExtensions[i].replace("*.", "");

                    if (self.Class.EXTENSIONSIMG.toString().indexOf(validExtensions[i]) >= 0) {
                        self.validOptions.image = true;
                    }
                }
            }
        }

        // Media is not supported then upload control is disabled
        if (!self.validOptions.image) {
            $(self.$contentFile).addClass("bz-file-upload__container--hidden");
        }

        // Limit of files is reached then upload control is disabled
        if (self.isMaxFilesReached() || !bizagi.util.media.fileAPISupported() ) {
            $(this.$contentFile).addClass("bz-file-upload__container--hidden");
        }
    },

    /**
     * onUploadOfflineFileCompleted
     * @param {} result
     */
    onUploadOfflineFileCompleted: function (fileName, mimeType, fileId) {
        var self = this;
        var $wrapper = $(".bz-files", self.getControl());

        var fileUploaded = {
            id: fileId,
            fileName: fileName,
            mimeType: mimeType,
            status: "new"
        };

        // Locate it before the upload wrapper
        var newItem = self.renderUploadItem(fileUploaded, self.files.length);
        $wrapper.append(newItem);

        // Increment counter
        self.filesCount = self.filesCount + 1;

        // Copy the files value but not its reference
        var files = self.files.slice();

        //Add the new item to this temporal array
        files.push(fileUploaded);

        // Update value
        self.setValue(files, false);

        // Check maxFiles
        self.checkMaxFiles(); 
    },

    /**
     * Renders a single upload item
     * @param file
     * @returns {*|jQuery}
     */
    renderUploadItem: function (file, index) {
        var self = this;
        var itemTemplate = self.getTemplateItemName();
        var template = self.renderFactory.getTemplate(itemTemplate);

        var html = $.fasttmpl(template, {
            url: "javascript:void(0);",
            id: file.id,
            allowDelete: self.properties.allowDelete || false,
            filename: file.fileName,
            index: index
        });

        return html;
    },

    /**
     * This render cannot be sent because it is full ajax
     * @returns {}
     */
    canBeSent: function () {
        return true;
    },

    /**
     * Fail action
     * @param {} evt
     * @returns {}
     */
    failReadFile: function (evt) {
        try {
            bizagi.log("Error code: " + evt.target.error.code);
        } catch (e) {
        }
    },

    /**
     * Fail action
     * @param {} error
     * @returns {}
     */
    onFail: function (error) {
        this._super(error);
    },

    /**
     * Method to remove item
     * @param item
     * @param id
     */
    removeOfflineFile: function (item) {
        var self = this;
        var file = item.data();
        var fileIndex = file.index;



        // Mark the file as deleted
        var fileValue = self.files[fileIndex];
        if( !fileValue){
            //Nothing to do
            return;
        }
        if(fileValue.status !== "new"){
            fileValue.status = "deleted";
        }
        else {
            fileValue.status = "new-deleted";
        }
        

        // Remove item
        item.hide();
        item.remove();

        // Update value
        self.setValue(self.files, false);

        // Check maxFiles
        self.checkMaxFiles();

        // Trigger change
        self.triggerRenderChange();
    },
    /**
     * Update the filesCount attribute based on the status (new|deleted) of each file
     */
    updateFilesCount: function(){
        var self = this;
        var files = this.properties.value;
        for(var i=0; i < files.length; i++){
            var status = files[i].status;
            if( status && status.includes("deleted") ) {
                self.filesCount= self.filesCount - 1;
            }
        }
    },
    /**
     * Sets the internal value
     * @param value
     * @param triggerEvents
     */
    setValue: function (value, triggerEvents) {
        var self = this;
        self._super(value, triggerEvents);
        self.updateFilesCount();
        if (self.filesCount === 0) {
            self.properties.method = "delete";
        }
    },
    /**
     * Check if the maximum number of files than can be uploaded has been reached
     * @returns {}
     */
    isMaxFilesReached: function () {
        var maxFiles = this.properties.maxfiles;
        return maxFiles !== 0 && (this.filesCount >= maxFiles);
    },

    openOfflineFile: function(item) {
        var self = this;
        var file = item.data();
        var dataIndex = file.index;
        var selectedValue = typeof (dataIndex) == "number" ? self.properties.value[dataIndex] : null;
        if (selectedValue) {
            var formParams = self.getFormParams();
            var getFileParams = {
                idCase: formParams.idCase,
                idWorkitem: formParams.idWorkitem,
                id: self.properties.xpath + '.' + selectedValue.id,
                offlineType: formParams.offlineType
            }
            bizagi.loading.start();
            $.when(self.dataService.getOfflineFile( getFileParams, "blob", selectedValue.mimeType))
            .done( function(binaryFile){
                var fileName = file.title;
                bizagi.util.media.handleBinaryFile( binaryFile, fileName);
            })
            .fail( function(error){
                console.log("Error opening offline file", error);
            })
            .always( function() {
                bizagi.loading.stop();
            });
        }
    },

    getBlobFromPath: function(path){
        var blobPromise = new $.Deferred();
        var self = this;

        if(self.isNative){
            var fileName = self.fileProperties.name;            
            if (!bizagi.util.isAndroidDevice()) {
                path = bizagi.util.media.normalizeIOSFilePath(path);
            }
            else{
                // Fix android 4.4 getting images from recent folder
                path = bizagi.util.media.getImagePath(path);
            }
            
            var errorHandler = function (error) {
                console.log('Error converting path to blob', error);  
                blobPromise.reject(error);
            };
    
            window.resolveLocalFileSystemURL(path, function (fileEntry) {
                fileEntry.file(function (file) {    
                    var reader = new FileReader();
                    reader.onloadend = function () {
                        var blob = new Blob([this.result], {type: file.type});
                        blob.name = fileName;
                        blobPromise.resolve(blob);
                    }
                    reader.readAsArrayBuffer(file);
                }, errorHandler);
            }, errorHandler);
        }
        else {
            //In browser is the File
            blobPromise.resolve(path);
        }
        return blobPromise;
    },

    saveOfflineFile: function(blobFile, fileId, mimeType){
        var self = this;
        var formParams = self.getFormParams();
        var parameters = {
            idCase: formParams.idCase,
            idWorkitem: formParams.idWorkitem,
            id: self.properties.xpath + '.' + fileId,
            operationType: "edition",
            offlineType: formParams.offlineType
        }        
        return self.dataService.saveOfflineFile(parameters, blobFile, mimeType);
    }
});
