/**
 *   Name: Bizagi Smartphone Render Date Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the date render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.date", bizagi.rendering.date.prototype);
// Extends itself
bizagi.rendering.smartphone.date.extend("bizagi.rendering.smartphone.date", {
    MAX_YEARS_DISPLAY: 100
}, {

    initializeData: function (data) {
        // Call base
        this._super(data);
    },

    /**
     * Method to render non editable values
     * @returns {*|jQuery}
     */
    renderReadOnly: function () {
        var value = this.getFormattedDate(this.getValue(), this.properties.showTime, true)
        value = bizagi.util.dateFormatter.formatDate(value, this.properties.fullFormat);
        var data = {value: value, editable: this.properties.editable};
        var template = this.renderFactory.getTemplate("readonly");
        return $.fasttmpl(template, data);
    },

    /**
     * Method to customize each render after processed in read-only mode
     */
    postRenderReadOnly: function () {
        this.input = $("input", this.getDateControl());
    },

    /**
     * To configure the mobiscroll plugin
     */
    postRender: function () {
        // Call base
        this._super();
        this.input = $("input", this.getDateControl());
    },

    configureHandlers: function () {
        var self = this;
        var controlElements = self.properties.showTime ? ["calendar", "time"] : ["calendar"];

        var defaultDateControl = self.getFormattedDate(self.properties.value, self.properties.showTime, true);
        var minDateControl = self.getFormattedDate(self.properties.minValue, self.properties.showTime, false);
        var maxDateControl = self.getFormattedDate(self.properties.maxValue, self.properties.showTime, false);

        var timeFormat = self.properties.showTime ? self.getTimeFormat() : "";

        // Adds a gap of years to show if the user doest not include min years
        if (isNaN(Date.parse(minDateControl))) {
            minDateControl = new Date();
            minDateControl.setFullYear(minDateControl.getFullYear() - self.Class.MAX_YEARS_DISPLAY);
            minDateControl.setHours(0, 0, 0, 0);
        }

        // Adds a gap of years to show if the user doest not include min years
        if (isNaN(Date.parse(maxDateControl))) {
            maxDateControl = new Date();
            maxDateControl.setFullYear(maxDateControl.getFullYear() + self.Class.MAX_YEARS_DISPLAY);
            maxDateControl.setHours(23, 59, 59);
        }

        $(".bz-input-icon", this.getControl()).mobiscroll().calendar({
            mode: "mixed", // Specify scroller mode like: mode: 'mixed' or omit setting to use default
            display: "bottom", // Specify display mode like: display: 'bottom' or omit setting to use default
            controls: controlElements,
            min: minDateControl,
            max: maxDateControl,
            defaultValue: defaultDateControl,
            timeFormat: timeFormat,
            showScrollArrows: true,
            lang: bizagi.util.languages[BIZAGI_LANGUAGE] || "en",
            headerText: "{value}",
            buttons: [
                "set",
                {
                    text: this.getResource("render-plugin-search-users-clear-button-name"),
                    handler: function (e, inst) {
                        self.date = undefined;
                        self.time = undefined;
                        self.timeS = undefined;
                        self.onChangeHandler(true);
                        inst.clear();
                    }
                },
                "cancel"
            ],
            onSet: function (valueText, inst) {
                var data = inst.getVal();
                self.date = {"day": data.getDate(), "month": data.getMonth(), "year": data.getFullYear()};

                //Checks if its has time, and included it
                if (self.properties.showTime) {
                    self.time = {"hours": data.getHours(), "minutes": data.getMinutes()};
                    if (!self.is24h) {
                        self.timeS = data.getHours() < 12 ? "am" : "pm";
                    }
                }

                self.onChangeHandler();
            },
            onBeforeShow: function (event, inst) {
                // Your custom event handler goes here
                self.triggerGlobalHandler("closeModalValidationMessage", {});
                self.triggerGlobalHandler("modalViewDidAppear", {});

                self.changeMinValue(self.properties.minValue);
                self.changeMaxValue(self.properties.maxValue);

                //TODO temporal fix in ios smartphone when a notification container is already displayed (QAF-6385 / MOB-171), review mobiscroll.min versions
                if (bizagi.util.isiOSDevice()) {
                    $(".ui-bizagi-notifications-container").css("display", "none");
                }
            },
            onClose: function (event, inst) {
                // Your custom event handler goes here
                self.triggerGlobalHandler("modalViewDidDisappear", {});
                $(".mbsc-fr-scroll").css("overflow-y", "auto");
            },
            onShow: function (event, inst) {
                $(".mbsc-fr-scroll").css("overflow-y", "hidden");
            }
        });

        // Bind event for change value
        this.input.on("focus", function (e) {
            e.preventDefault();
            e.stopPropagation();

            if (bizagi.util.isCordovaSupported() && bizagi.util.isAndroidDevice()) {
                cordova.plugins.Keyboard.disableScroll(true);
                cordova.plugins.Keyboard.close();
            }
        });

        //Set input displayValue if it has default value
        if (self.properties.displayValue === "" && self.properties.value) {
            var date = self.getFormattedDate(self.properties.value, self.properties.showTime, true, false);
            if (typeof (date) !== "undefined") {
                var displayValue = bizagi.util.dateFormatter.formatDate(date, self.properties.fullFormat);
                this.input.prop("value", displayValue);
            }
        }

        this.input.attr("keyboard", "disable");
    },

    /**
     * Configure the timeformat
     */
    getTimeFormat: function () {
        if (this.properties.timeFormat.indexOf("H") !== -1 || this.properties.timeFormat.indexOf("HH") !== -1) {
            this.is24h = true;
            return "HH:ii";
        } else {
            this.is24h = false;
            return "hh:ii A";
        }
    },

    /**
     * Get Date whit the Invariant and ISO Format applied.
     */
    getFormattedDate: function (date, showTime, initializer) {
        var self = this;
        
        if (!date || bizagi.util.isObjectEmpty(date)) return;
        date = (typeof date != "string") ? date.toString() : date;

        //There are some dates that haven't time,... actions and validations for example or when we have to put a initial value (default value without time),
        //in that cases we should identify the case and if the date haven't the time value, we have to add (00:00:00) it to can apply the format (getDateFromInvariant).
        var time = (showTime && (self.getTime() === "0:0" || initializer)) ? " " + date.substr(date.length - 8, date.length) : "";

        var currentDate = (initializer && showTime && time.split("/").length > 1) || (date.split(":").length === 1) ? date += " 00:00:00" : date;
        date = bizagi.util.dateFormatter.getDateFromInvariant(currentDate, showTime);

        var currentTime = showTime ? time !== "" ? time : " " + self.getTime() : " 00:00";
        var fullDateFormated = self.setHour(self.getFullFormatedDate(date, currentTime));

        fullDateFormated = bizagi.util.dateFormatter.getDateFromISO(fullDateFormated, true);

        return fullDateFormated;
    },

    /**
     * Changes the render min value
     * @param {} value
     */
    changeMinValue: function (value) {
        var self = this;
        var properties = self.properties;
        var dateControl = self.getDateControl();

        // Set value in control
        if (value && self.properties.editable) {
            if (typeof (value) === "number") {
                value = new Date(value);
                dateControl.mobiscroll('option', {min: value});
                value = bizagi.util.dateFormatter.formatInvariant(value, properties.showTime); // Set as invariant
            } else {
                dateControl.mobiscroll('option', {min: bizagi.util.dateFormatter.getDateFromInvariant(value, true)});
            }

            properties.minValue = value;
        }
    },

    /**
     * Changes the render max value
     * @param {} value
     */
    changeMaxValue: function (value) {
        var self = this;
        var properties = self.properties;
        var dateControl = self.getDateControl();

        // Set value in control
        if (value && self.properties.editable) {
            if (typeof (value) === "number") {
                value = new Date(value);
                dateControl.mobiscroll('option', {max: value});
                value = bizagi.util.dateFormatter.formatInvariant(value, properties.showTime); // Set as invariant
            } else {
                dateControl.mobiscroll('option', {max: bizagi.util.dateFormatter.getDateFromInvariant(value, true)});
            }

            properties.maxValue = value;
        }
    },

    /**
     * Set the display value on input
     */
    setDisplayValue: function (value) {
        var self = this;
        var date = self.getFormattedDate(value, self.properties.showTime, false);
        var displayValue = self.preprocessDateValue(date);

        self.input = self.input ? self.input : self.getDateControl();

        if (self.properties.editable === false) {
            // Render as simple value
            if (typeof (displayValue) === "string" || typeof (displayValue) === "number") {
                self.input.html(displayValue);
            }
        } else {
            self.input.prop("value", displayValue);
        }
    },

    /**
     * Get the DOM Control
     */
    getDateControl: function () {
        return $(".bz-input-icon", this.getControl());
    },

    /**
     * Get Time from mobiscroll
     */
    getTime: function () {
        var self = this;

        //Converting the time in 24h format.
        if (self.properties.showTime && (self.time && self.time["minutes"] !== "")) {
            var time = self.time["hours"] + ":" + self.time["minutes"];
            time += self.timeS;

            if (time.indexOf("am") != -1 && self.time["hours"] == 12) {
                time = time.replace("12", "0");
            }

            if (time.indexOf("pm") != -1 && self.time["hours"] < 12) {
                time = time.replace(self.time["hours"], (parseInt(self.time["hours"]) + 12));
            }

            return time.replace(/(am|pm)/, '');
        } else {
            return "";
        }
    },

    /**
     * Get Date from mobiscroll
     * */
    getDate: function () {
        if (this.date && this.date["month"] !== "") {
            return this.date["year"] + "-" + (parseInt(this.date["month"]) + 1) + "-" + this.date["day"];
        } else {
            return "";
        }
    },

    /**
     * Set the control value when the control change
     */
    onChangeHandler: function (isClearButton) {
        var self = this;
        var properties = self.properties;
        var currentDate = self.getDate();
        var currentTime = (properties.showTime) ? " " + self.getTime() : " 0:00";

        // If curren date is empty reset actual value
        if ((currentDate === "" && !properties.showTime) || (currentDate === "" && currentTime === "") || (isClearButton)) {
            self.setValue("");
            self.input.prop('value', "");
            properties.displayValue = "";
        } else {
            var formatedFullDate = self.setHour(self.getFullFormatedDate(currentDate, currentTime));

            formatedFullDate = bizagi.util.dateFormatter.getDateFromISO(formatedFullDate, true);

            if (!formatedFullDate || formatedFullDate == 0) return;

            // Set date value
            self.setNewDateValue(formatedFullDate, properties.showTime);

            var displayValue = self.preprocessDateValue(formatedFullDate);
            properties.displayValue = displayValue;

            self.input.prop("value", self.properties.displayValue);
        }
    },

    /**
     * Apply the final format that will be showed on the input
     */
    getFullFormatedDate: function (currentDate, currentTime) {
        var fullDate = "";

        if (currentDate.length > 0) {
            fullDate = currentDate;
        } else if (currentDate !== undefined) {
            fullDate = bizagi.util.dateFormatter.formatISO(currentDate, false);
        } else {
            fullDate = bizagi.util.dateFormatter.formatISO(new Date(), false);
        }

        fullDate += currentTime;
        var dateFromFormat = bizagi.util.dateFormatter.getDateFromFormat(fullDate, "yyyy-MM-dd H:m");

        return bizagi.util.dateFormatter.formatDate(dateFromFormat !== 0 ? dateFromFormat : currentDate, "yyyy-MM-dd H:mm:ss");
    },

    /**
     * Set the control value
     */
    setNewDateValue: function (formatedFullDate, showTime) {
        var value = "";

        if (formatedFullDate != "")
            value = bizagi.util.dateFormatter.formatInvariant(formatedFullDate, showTime);

        this.setValue(value);
    },

    /**
     * Removing the time when be necesary
     */
    applyDateFormat: function (date, showTime) {
        if (!showTime) {
            var initIndexTime = null;

            if (date.indexOf("00:00") != -1) {
                initIndexTime = date.indexOf("00:00");
            } else if (date.indexOf("0:00") != -1) {
                initIndexTime = date.indexOf("0:00");
            } else if (date.indexOf("12:00") != -1) {
                initIndexTime = date.indexOf("12:00");
            } else {
                initIndexTime = "-1";
            }

            var endIndexTime = initIndexTime + 8;
            date = initIndexTime === -1 ? date : date.replace(date.substring(initIndexTime, endIndexTime), "").replace(" am", "").replace(" pm", "");
        }

        return date;
    },

    /*
     *  Checks if the date could be localized using site language
     */
    preprocessDateValue: function (date) {
        return bizagi.util.dateFormatter.formatDate(date, this.properties.fullFormat);
    },

    /**
     * Public method to determine if a value is valid or not
     */
    isValid: function (invalidElements) {
        var self = this;
        var properties = self.properties;
        var bValid = true;
        var value = self.getValue();

        if (value) {
            if (bizagi.util.parseBoolean(properties.editable)) {
                value = bizagi.util.dateFormatter.getDateFromInvariant(value);

                if (value != 0) {
                    // Clear time for validations
                    value.setHours(0, 0, 0, 0);

                    // Check min value and max value
                    if (properties.minValue && typeof (properties.minValue) === 'string') {
                        bValid = self.validateMinAndMaxValue(value, true, properties, invalidElements) ? false : bValid;
                    }

                    if (properties.maxValue && typeof (properties.maxValue) === 'string') {
                        bValid = self.validateMinAndMaxValue(value, false, properties, invalidElements) ? false : bValid;
                    }
                }
            }
        }

        if (bizagi.util.parseBoolean(properties.editable)) {
            // Check required
            if (properties.required && !self.hasValue()) {
                self.showMandatoryMessage(properties, invalidElements);
            }
        }

        if (bValid) {
            // Clear error message
            self.setValidationMessage("");
        }

        return bValid;
    },

    /**
     * To validate the min and max values
     * @param {} value
     * @param {} isMinValue
     * @param {} properties
     * @param {} invalidElements
     */
    validateMinAndMaxValue: function (value, isMinValue, properties, invalidElements) {
        var vValue = bizagi.util.dateFormatter.getDateFromInvariant(isMinValue ? properties.minValue : properties.maxValue);

        //In case the value from the minValue or maxValue are assigned from a Date control that does not have the option  "showTime!
        if (vValue == 0) {
            var resultValue = "";
            if (properties.timeFormat.indexOf(":ss") == -1) {
                if (isMinValue) {
                    resultValue = properties.minValue + ":00";
                } else {
                    resultValue = properties.maxValue + ":00";
                }
            }

            vValue = bizagi.util.dateFormatter.getDateFromFormat(resultValue, properties.fullFormat + ":ss");
        }

        vValue.setHours(0, 0, 0, 0);

        if ((isMinValue && value < vValue) || (!isMinValue && value > vValue)) {
            var formatedDate = bizagi.util.dateFormatter.formatDate(vValue, properties.fullFormat);
            var message = "";

            if (isMinValue) {
                message = this.getResource("render-date-minimum-validation").replaceAll("#label#", properties.displayName).replaceAll("#minValue#", formatedDate);
            } else {
                message = this.getResource("rendr-date-maximum-validation").replaceAll("#label#", propertiee).replaceAll("#maxValue#", formatedDate);
            }

            invalidElements.push({xpath: properties.xpath, message: message});
            return true;
        }

        return false;
    },

    /**
     * To show the mandatory values message
     * */
    showMandatoryMessage: function (properties, invalidElements) {
        var self = this;
        var message = self.getResource("render-required-text").replaceAll("#label#", properties.displayName);

        invalidElements.push({xpath: properties.xpath, message: message});

        return false;
    },

    setHour: function (date) {
        return date.replace(" 0:", " 00:").replace(" 1:", " 01:").replace(" 2:", " 02:").replace(" 3:", " 03:").replace(" 4:", " 04:").replace(" 5:", " 05:").replace(" 6:", " 06:").replace(" 7:", " 07:").replace(" 8:", " 08:").replace(" 9:", " 09:");
    },

    /**
     * Cleans the control fields as it requires
     */
    clearDisplayValue: function () {
        var self = this;

        self.date = undefined;
        self.time = undefined;
        self.timeS = undefined;
        self.onChangeHandler(true);
        self.setDisplayValue('');
    },
    isValidMinMaxValue: function () {
        var self = this;
        var isValid = true;
        var value = self.getValue();

        if (value) {
            value = bizagi.util.dateFormatter.getDateFromInvariant(value);

            if (value !== 0) {
                // Clear time for validations
                value.setHours(0, 0, 0, 0);

                // Check min value and max value
                if (self.properties.minValue && typeof (self.properties.minValue) === 'string')
                    isValid = self.validateMinAndMaxValue(value, true);

                if (self.properties.maxValue && typeof (self.properties.maxValue) === 'string')
                    isValid = self.validateMinAndMaxValue(value, false);
            }
        }

        return isValid;
    },

    validateMinAndMaxValue: function (currentDate, isMinValue) {
        var self = this;
        var date = isMinValue ? self.properties.minValue : self.properties.maxValue;
        var formattedDate = bizagi.util.dateFormatter.getDateFromInvariant(date);

        formattedDate.setHours(0, 0, 0, 0);

        return !((isMinValue && currentDate < formattedDate) || (!isMinValue && currentDate > formattedDate));

    },

    getElementsToStylize: function ($context) {        
        return $("input", $context); 
    }
});