/**
 *   Name: Bizagi Smartphone Link Column Decorator Extension
 *   Author: Bizagi UI Forms
 *   Comments:
 *   -   This script will redefine the number column decorator class to adjust to smartphone devices
 */

// Extends from column
bizagi.rendering.smartphone.columns.column.extend("bizagi.rendering.smartphone.columns.link", {}, {

    applyOverrides: function (decorated) {
        var self = this;
        var mode = self.getMode();

        self._super(decorated);

        if (mode !== "design" && mode !== "layout") {
            self.surrogateKey = decorated.surrogateKey;

            decorated.getXpath = self.getXpath;
            decorated.properties.xpathContext = self.grid.properties.xpath + "[id=" + self.surrogateKey + "]";

            if (self.grid.properties.xpathContext && decorated.properties.xpathContext.indexOf(self.grid.properties.xpathContext) === -1) {
                decorated.properties.xpathContext = self.grid.properties.xpathContext + "." + decorated.properties.xpathContext;
            }

        }

        if (decorated.properties.type === "columnFormLink") {
            decorated.getFormLinkXpath = this.getFormLinkXpath;
        }
    },

    /**
     * Apply custom overrides to each decorated instance
     * @param {} xpath
     * @returns {}
     */
    getXpath: function (xpath) {
        var xpathPath = xpath;
        var index = xpathPath.indexOf(this.grid.properties.xpath);

        if (index !== -1) {
            xpathPath = xpathPath.substr(index + this.grid.properties.xpath.length + 1);
        }

        return this.grid.properties.xpath + "[id=" + this.surrogateKey + "]." + xpathPath;
    },

    /**
     * Returns the xpath to be used
     * @returns {}
     */
    getContextXpath: function () {
        return this.grid.properties.xpath + "[id=" + this.surrogateKey + "]";
    },

    /**
     * Get Form Link xpath to use
     * @returns {*}
     */
    getFormLinkXpath: function () {
        // Calculate layout properties to maximized property
        this.properties.maximized = bizagi.util.parseBoolean(this.properties.maximized) !== null ? bizagi.util.parseBoolean(this.properties.maximized) : false;
        this.calculateInitialLayoutProperties();

        if (!this.properties.xpath) return;
        return this.properties.xpath;
    }

});
