/*
*   Name: Bizagi Smartphone Column Decorator Extension
*   Author: Bizagi UI Forms
*   Comments:
*   -   This script will redefine the column decorator class to adjust to smartphone devices
*/

// Extends from columns smartphone base
bizagi.rendering.smartphone.columns.column.extend("bizagi.rendering.smartphone.columns.readonly", bizagi.rendering.columns.readonly.prototype);
// Extends itself
bizagi.rendering.smartphone.columns.readonly.extend("bizagi.rendering.smartphone.columns.readonly", {}, {

    render: function(surrogateKey, value) {
        var template = this.renderFactory.getTemplate("cell.readonlycontrol");
        var valueToDisplay = "";
        if(value != null && value != undefined) {
            value = bizagi.util.sanitizeHTML(value);
            valueToDisplay = value.replaceAll("\\n", "<br/>").replaceAll("\n", "<br/>");
        }
        // Support for multiline text
        return $.fasttmpl(template, {value: valueToDisplay});
    }
});