/**
 *   Name: Bizagi Smartphone Tab Container Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the container class to adjust to smartphone devices
 */

// Extends from container smartphone base
bizagi.rendering.smartphone.container.extend("bizagi.rendering.smartphone.tab", bizagi.rendering.tab.prototype);
// Auto extend
bizagi.rendering.smartphone.tab.extend("bizagi.rendering.smartphone.tab", {}, {

    /* POST RENDER CONTAINER ACTIONS
    =================================================*/
    postRenderContainer: function (container) {
        var self = this;

        this._super(container);

        container.material_bizagi_tabs({
            direction: this.properties.orientation,
            active_index_tab: this.activeTab
        }).on("updateActive", function (ev, info) {
            ev.stopPropagation();

            self.setActiveTab(info.index);
        });

        $("> .bz-tabs > .bz-tab", container).rippleEffect();
    }

});
