/*
*   Name: Bizagi Smartphone Column Decorator Extension
*   Author: Bizagi UI Forms
*   Comments:
*   -   This script will redefine the column decorator class to adjust to smartphone devices
*/

bizagi.rendering.smartphone.columns.column.extend("bizagi.rendering.smartphone.columns.combo", {}, bizagi.rendering.columns.combo.prototype );
// Extends itself
bizagi.rendering.smartphone.columns.combo.extend("bizagi.rendering.smartphone.columns.combo", {}, {
    applyOverrides: function(decorated) {
        // Hacks the getControl method in the decorated render to add features
        var self = this;
        self._super(decorated);

        if (decorated && decorated.getControl) {
            self.originalGetControl = decorated.getControl;
            decorated.getControl = self.getControl;
            decorated.properties.displayType = "normal";
        }
    },

    /*
    *   Template method to get the control element
    *   This code runs under the decorated element context
    */
    getControl: function() {
        var self = this;
        var result = self._super();

        if (!result || result.length == 0) {
            result = $(".ui-bizagi-cell-readonly", self.element || self.observableElement);
        }

        return result;
    },
     /*
    *   Sets the internal value
    */
   setValue: function(surrogateKey, value) {
    var self = this;
    
    // Check is offline form	    
    var isOfflineForm = bizagi.util.isOfflineForm({ context: self });
    if(isOfflineForm && (typeof (value) == "number" || typeof (value) == "string")){
        var newValue =  typeof (self.properties.data) == "object" ? self.properties.data.find(function(item){ return item.id == value }) : value;
        var decorated = self.getDecorated(surrogateKey);
        if (decorated !== null) {
            decorated.selectedValue = value;
        }
        return self._super(surrogateKey, newValue);
    }
    else {
        return self._super(surrogateKey, value);
    }
}

});
