/**
 *   Name: Bizagi Smartphone Render Button Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the button render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.button", bizagi.rendering.button.prototype);
// Extends itself
bizagi.rendering.smartphone.button.extend("bizagi.rendering.smartphone.button", {}, {

    /**
     * Template method to implement in each device to customize the render's behaviour to add handlers
     * @returns {}
     */
    configureHandlers: function () {
        var self = this;

        $(":button", self.getControl()).on("click", function () {
            self.processButton();
        });
    },

    /**
     * Process the button actions workflow
     * @returns {}
     */
    processButton: function () {
        var self = this;

        if (self.properties.editable) {

            // Check if action need conformation
            if (self.properties.needsUserConfirmation) {
                $.when(bizagi.showConfirmationBox(self.properties.userConfirmationMessage, "Bizagi", "warning"))
                    .done(function () {
                        // Trigger change
                        self.triggerRenderChange({pressed: true});
                    });
            } else {
                if (!self.properties.preventDefault)
                    self.runButtonAction();

                // Trigger change
                self.triggerRenderChange({pressed: true});
            }
        }
    },

    getElementsToStylize: function($context){
        return  $("button", $context);         
    }
});
