/**
 *   Name: Bizagi Smartphone Render Search Extension
 *   Author: Bizagi UI Forms
 *   Comments:
 *   -   This script will redefine the search render class to adjust to smartphone devices
 */

// Extends from render smartphone base
bizagi.rendering.smartphone.render.extend("bizagi.rendering.smartphone.search", bizagi.rendering.search.prototype);
// Extends itself
bizagi.rendering.smartphone.search.extend("bizagi.rendering.smartphone.search", {
    ADVANCED_SEARCH_ID: -1,
    ADVANCED_ADDITION_ID: -2,
    CLEAR_SEARCH_ID: -3,
    NO_RESULTS_SEARCH_ID: -4,
    DEFAULT_SEARCH_ID: "",
    SEARCH_MIN_LENGTH: 3,
    SEARCH_DELAY: 250,
    SEARCH_MAX_RECORDS: 50
}, {

    /**
     * Update or init the element data
     * @param {} data submitData
     * @returns {}
     */
    initializeData: function (data) {
        var self = this;

        // Call base
        self._super(data);

        // Check if offline forms is supported
        this.isOfflineForm = bizagi.util.isOfflineForm({context: this});
        this.properties.submitOnChange = this.isOfflineForm ? false : this.properties.submitOnChange;

        // Store reference of all ajax request in order to run only one instance of them
        this.deferredReference = null;
    },

    renderReadOnly: function () {
        var template = this.renderFactory.getTemplate("readonly");
        var value = this.getDisplayValue();
        var label = value.label || value.value;

        if (typeof label != "undefined") {
            if (typeof label == "object")
                label = label.join(" - ");
        } else {
            label = "";
        }

        if (typeof label == "string") {
            if (value.additionallabel) {
                label += " - " + value.additionallabel;
            }

            label = bizagi.util.sanitizeHTML(label);
        }

        return $.fasttmpl(template, {
            value: label,
            editable: this.properties.editable
        });
    },

    /**
     * Template method to implement in each device to customize each render after processed
     * @returns {}
     */
    postRender: function () {
        this.searchInput = $(".bz-input-icon", this.getControl());
    },

    postRenderReadOnly: function () {
        this.searchInput = $(".bz-input-icon", this.getControl());
    },

    /**
     * Template method to implement in each device to customize the render's behaviour
     * to add handlers
     * @returns {}
     */
    configureHandlers: function () {
        var self = this;
        var control = self.getControl();

        // Select new item
        $(".bz-input-icon", control).on("click", function () {
            if (!self.properties.advancedSearch || self.isOfflineForm) {
                self._simplySearchView();
            } else {
                self._advanceSearchView();
            }
        });

        $(".bz-icn--clear", control).bind("click", function (event) {
            // Change display icon
            $(".bz-input-icon", control).addClass("bz-input-icon--empty");

            // Call clean data
            self.cleanData();

            // Submit info to server
            self.readyActionExecution().done(function () {
                if (self.properties.submitOnChange) {
                    self.submitData();
                }
            });

            event.stopPropagation();
        });

    },

    /**
     * Configure the modalView Handlers for the new suggest control
     * @param {} inputContainer
     * @returns {}
     */
    configureModalViewHandlers: function (modalView) {
        var self = this;

        // Configuring list as a selectable element
        self.modalViewList = $(".bz-list", modalView.content);

        // Suggest
        var suggestFilter = bizagi.util.debounce(function (value) {
            self.autoCompleteSearchResults($.trim(value));
        }, self.Class.SEARCH_DELAY);

        $("[data-action=close]", modalView.header).bind("click", function () {
            modalView.close();
        });

        var $clearButton = $(".bz-modal-view__clear", modalView.header);
        var $searchInput = $(".bz-modal-view__search", modalView.header);

        $clearButton.bind("click", function () {
            $searchInput.val("").trigger("clean");
        });

        // Setting the initial selected display value
        $(".bz-list", modalView.content).on("click", "li", function () {
            var item = $.data(this).item;
            if (!item || (item && item.id == self.Class.NO_RESULTS_SEARCH_ID))
                return;

            self.selectItem(item);
            modalView.close();
        });

        // Filtering list
        $searchInput.bind("clean keyup", function () {
            if (this.value == "") {
                $clearButton.removeClass("bz-modal-view__clear--visible");
            } else {
                $clearButton.addClass("bz-modal-view__clear--visible");
            }
            suggestFilter(this.value);
        }).on("focus", function () {
            modalView.header.addClass("bz-modal-view__header--active");
        }).on("blur", function () {
            modalView.header.removeClass("bz-modal-view__header--active");
        });


    },

    /**
     * Opens the search slide
     * @private
     */
    _advanceSearchView: function () {
        var self = this;
        var properties = self.properties;
        var formSearchParams = $.extend({}, self.getParams(), {
                navigation: self.getFormContainer().getNavigation()
            }
        );

        var searchForm = new bizagi.rendering.smartphone.helpers.searchForm(
            self.dataService, self.renderFactory,
            properties.searchForms, {
                allowFullSearch: properties.allowFullSearch,
                maxRecords: properties.maxRecords,
                allowNew: properties.allowNew || false,
                orientation: properties.orientation || "ltr",
                editable: properties.editable
            },
            formSearchParams
        );

        searchForm.renderEdition({
            idRender: properties.id,
            xpath: properties.xpath,
            xpathContext: properties.xpathContext,
            displayName: properties.displayName,
            idPageCache: properties.idPageCache
        }).done(function (itemId) {
            self.setValue({id: itemId});
            
            self.readyActionExecution().done(function () {
                var form = self.getFormContainer();
                if (!self.properties.submitOnChange) {                    
                    $.when(form.submitData()).done(function () {
                        self.refreshControl();
                    });                    
                }                
            });
        }) ;
    },

    /**
     * Simply search autocomplete
     * @private
     */
    _simplySearchView: function () {
        var self = this;
        var content = self.getContentTemplate();

        self.searchInput.mobileView({
            customHeader: $.fasttmpl(self.renderFactory.getTemplate("modal-header-search"), {}),
            orientation: self.properties.orientation || "",
            contentTemplate: content,
            transition: "slideIn:up",
            close: function () {
                self.triggerGlobalHandler("modalViewDidDisappear", {});
            },
            afterOpenAnimation: function (event) {
                $(".bz-modal-view__search", event.view.header).focus();
            },
            open: function (event) {
                self.triggerGlobalHandler("closeModalValidationMessage", {});
                self.triggerGlobalHandler("modalViewDidAppear", {});
            },
            configureActions: function (context) {
                self.configureModalViewHandlers(context);
            }
        });
    },

    autoCompleteSearchResults: function (currentValue) {
        var self = this;

        $.when(self.processRequest({term: currentValue}))
            .done(function (response) {

                self.modalViewList.find("li").remove();

                for (var i = 0, length = response.length; i < length; i++) {
                    var searchItem = self.renderSearchItem(response[i]);

                    searchItem.appendTo(self.modalViewList);
                }
            });
    },

    renderSearchItem: function (item) {
        var self = this;
        var template = self.renderFactory.getTemplate("search.item");

        var searchItem = $.tmpl(template, {
            id: item.id,
            label: item.label,
            value: item.value
        });

        searchItem.data("item", item);

        return searchItem;
    },

    processRequest: function (req) {
        var self = this;
        var properties = self.properties;
        var deferred = new $.Deferred();

        // Create array for response objects
        var suggestions = [];

        if (self.properties.allowSuggest && (req.term.length >= self.properties.searchLength)) {

            // Update term property to reflect the search
            properties.term = req.term;

            var getSuggestData = self.isOfflineForm ? self.getOfflineData() : self.getData();

            // Cancel previous request in order to improve the performance
            if (self.deferredReference && typeof (self.deferredReference.reject) === "function" && self.deferredReference.state() !== "resolved") {
                self.deferredReference.reject({hideStatus: true});
            }

            self.deferredReference = getSuggestData.done(function (data) {
                if (data && data.length > 0) {
                    data = data.slice(0, properties.maxRecords);

                    if (data.length > self.Class.SEARCH_MAX_RECORDS) {
                        data = data.slice(0, self.Class.SEARCH_MAX_RECORDS);

                        suggestions.push({
                            id: self.Class.NO_RESULTS_SEARCH_ID,
                            label: self.getResource("render-search-maximum-records-allowed"),
                            value: ""
                        });


                    } else {
                        data = data.slice(0, properties.maxRecords);
                    }

                    // Default value
                    suggestions.push({
                        id: self.Class.DEFAULT_SEARCH_ID,
                        label: self.getResource("render-combo-empty-value"),
                        value: ""
                    });

                    $.each(data, function (i, val) {
                        if (properties.maxRecords === 0 || i < properties.maxRecords) {
                            var parseValue = bizagi.util.highlightMatches(val, req.term);

                            suggestions.push({id: val.id, label: parseValue, value: val.value});
                        }
                    });

                } else {
                    suggestions.push({
                        id: self.Class.NO_RESULTS_SEARCH_ID,
                        label: self.getResource("render-grid-no-records"),
                        value: ""
                    });
                }

                deferred.resolve(suggestions);

            }).fail(function () {
                deferred.reject();
            });

        } else {
            deferred.resolve(suggestions);
        }

        return deferred.promise();
    },

    /**
     * Sets the value to show and calls the method to sets the control value
     * @param {object} value
     * @param {int} value.id
     * @param {string} value.label
     * @param {string=} value.additionallabel
     * @example
     *   value = {
     *     id: 123,
     *     label: "xx",
     *     additionallabel: "yyyy"
     *   }
     */
    setDisplayValue: function (value) {
        var self = this;
        var label = "";

        value = self.mapValue(value);

        if (value !== null && typeof value !== "undefined" && typeof value.id !== "undefined") {
            if (value.additionallabel && value.label) {
                label = value.label + " - " + value.additionallabel;
            } else if (value.label) {
                label = value.label;
            } else {
                label = "";
            }

            var inputText = self.searchInput.find("input");
            inputText.val(label);
            inputText.attr("id", value.id);

            if (value.id !== "" && self.properties.allowClear) {
                $(".bz-input-icon", self.getControl()).removeClass("bz-input-icon--empty");
            }

            if (self.properties.editable) {
                self.setValue(value, true);
            }
        }
    },

    /**
     * on the smartphone only sends the id to submit in the smartphone it send the id and the value
     * @returns {}
     */
    submitData: function () {
        var self = this;
        var data = {};

        // Add current data
        data[self.properties.xpath] = self.getValue();

        // Executes submit on change
        if (self.properties.submitOnChange && data[self.properties.xpath].id != undefined)
            self.submitOnChange(data, true);
    },

    /**
     * Cleans the control's data
     * @returns {}
     */
    cleanData: function () {
        var value = {id: "", value: ""};

        if (bizagi.util.isEmpty(this.value)) {
            value = {};
        }

        this.setValue(value);
        this.setDisplayValue(value);
    },

    getContentTemplate: function () {
        var template = this.renderFactory.getTemplate("search.items");
        var content = $.fasttmpl(template, {});

        return $.trim(content);
    },

    selectItem: function (item) {
        var self = this;
        var previousValue = self.hasValue();
        var label = Array.isArray(item.value) ? item.value.join(" - ") : item.value;

        self.setDisplayValue({id: item.id, label: label});

        if (self.properties.allowClear) {

            if (item.id === self.Class.DEFAULT_SEARCH_ID) {
                self.searchInput.find(".gn-bz-search__clear").addClass("gn-bz-search--hidden");
                self.searchInput.find(".gn-bz-search__icon").removeClass("gn-bz-search--hidden");
            } else {
                if (!previousValue && item.id !== self.Class.DEFAULT_SEARCH_ID) {
                    self.searchInput.find(".gn-bz-search__clear").removeClass("gn-bz-search--hidden");
                    self.searchInput.find(".gn-bz-search__icon").addClass("gn-bz-search--hidden");
                }
            }
        }
    },

    /**
     * Get offline data for request
     * @param request
     */
    getOfflineData: function () {
        var self = this;
        var dataList = self.properties.data || [];
        var filter = (self.properties.term || "").toLowerCase();

        return $.when(filter.startsWith("%%") ? dataList : dataList.filter(function (item) {
            var value = item && item.value ? item.value : "";
            if (typeof (value) === "object") {
                value = value.toString();
            }

            return value.toLowerCase().indexOf(filter) > -1;
        }));
    }

});
